.chart-row {
    padding-top: $measure-large;
    padding-bottom: $measure-large;
    margin-top: $measure-large;
    margin-bottom: $measure-large;

    border-top: 1px dashed $color-quill;
    border-bottom: 1px dashed $color-quill;

    h2 {
        text-align: center;
    }
}

.chart-row__items {
    @extend %grid;
}

.chart-row__items__chart {
    @include grid-column(6, 'small');
    margin-bottom: $measure-large;
}

.chart-row__items__chart__row__title {
    text-align: center;
    text-transform: uppercase;
    color: $color-emperor;
    font-weight: $font-weight-semibold;
    margin-bottom: $measure-medium;
}

.chart-row__items__chart__row {
    @extend %grid;
}

.chart-row__items__chart__row__title,
.chart-row__items__chart__row__graph {
    @include grid-column(6, 'min');
}

.chart-row__items__chart__row__legend {
    @include grid-column(6, 'min');
}

$chart-themes: ('default' (#a4cacc #724289 #c7637d #63c781 #c7b763));

@each $theme in $chart-themes {
    $theme-name: nth($chart-themes, 1);
    $theme-colors: nth($chart-themes, 2);
    .chart-row__items__chart#{if($theme-name == 'default', '', '--' + $theme-name)} {
        @each $color in $theme-colors {
            $i: index($theme-colors, $color);
            path:nth-child(#{$i}) {
                stroke: $color;
            }

            .chart-row__items__chart__row__legend__item:nth-child(#{$i}) {
                .fa.fa-circle {
                    color: $color;
                }
            }
        }
    }
}

.chart-row__items__chart__row__legend__item {
    margin-bottom: $measure-small;

    &:not(:last-child) {
        padding-bottom: $measure-small;
        position: relative;

        &:after {
            content: '';
            position: absolute;
            bottom: 0;
            left: 1rem;
            background: $color-quill;
            height: 1px;
            width: 50%;
        }
    }

    @include font-family(lato);
    font-weight: $font-weight-bold;
    font-size: $font-size-delta;
}

.chart-row__items__chart__value {
    @extend %animate--svg-path-target;
}

.chart-row__items__chart__row__legend__item__label {
    margin-left: 1rem;
    color: $color-emperor;
    font-size: $font-size-default;
    font-weight: $font-weight-default;
}
