///
// Footer Meta
// @group components
///

.footer-meta {
    position: relative;
    line-height: $line-height-perfect-fifth;
    width: 100%;
    align-self: flex-end;

    .footer-meta__card {
        .footer-meta__card__img {
            img {
                width: 100%;
                margin: $measure-small 0;
            }
        }
    }
}

.footer-meta {
    position: relative;
    &:before {
        @include absolute(top);
        @include non-selectable;
        content: '';
        width: 200vw;
        height: 100%;
        pointer-events: none;

        @include media('<=medium') {
            max-width: calc(100% + #{$measure-medium});
        }
    }

    &.section--hub {
        &:before {
            background-color: $color-white;
        }
    }

    &.section--undergraduate {
        &:before {
            background-color: $color-flamingo;
        }
    }

    &.section--executive-education {
        &:before {
            background-color: $color-vivid;
        }
    }

    &.section--masters {
        &:before {
            background-color: $color-prussian;
        }
    }

    &.section--mba {
        &:before {
            background-color: $color-sherpa;
        }
    }

    &.section--phd,
    &.section--executive-phd {
        margin-top: $measure-enormous;

        &:before {
            background-color: $color-allports;
        }
    }

    &.section--corporate {
        &:before {
            background-color: $color-concrete;
        }
    }
}

$footer-themes: (
    hub: (
        $color-white,
    ),
    corporate: (
        $color-concrete,
    ),
    undergraduate: (
        $color-flamingo,
    ),
    masters: (
        $color-prussian,
    ),
    phd: (
        $color-allports,
    ),
    mba: (
        $color-sherpa,
    ),
    executive-education: (
        $color-vivid,
    ),
);

@each $theme, $colors in $footer-themes {
    $bg-color: nth($colors, 1);

    .scp--#{$theme} {
        .footer-meta {
            &.section--scp-theme {
                &:before {
                    background-color: $bg-color;
                }
            }
        }
    }
}

.footer-meta__card {
    @include media('<=small') {
        margin-bottom: $measure-medium;
    }
    @include grid-column(6, small);

    h5 {
        font-size: $font-size-small;
        font-weight: $font-weight-bold;
        text-transform: uppercase;
        color: $color-lucifer;
    }

    a {
        vertical-align: top;
        word-wrap: break-word;
    }
}

.footer-meta__card__wrapper {
    padding: $measure-medium;
    box-shadow: 0 0 1.5rem 3px rgba(0, 0, 0, 0.15);
    background-color: $color-white;
    height: 100%;

    @include media('<=small') {
        width: auto;
    }
    @include media('>small') {
        width: 100%;
    }
}

.footer-meta__card__social {
    margin-top: $measure-medium;

    a {
        @include anchor(inline-block);
        margin-right: $measure-small;
    }

    .footer-meta__card__social__icons {
        display: inline;
        font-size: $font-size-delta;
    }
}

/*
 * footer social media cards
 */
.socialmedia-card__logo {
    font-size: ($font-size-gamma);
    color: $color-dove-gray;
    text-align: center;
    line-height: 1;
    margin-bottom: $measure-medium;
}

.socialmedia-card__title {
    color: $color-dove-gray;
    line-height: $line-height-default;
    margin-bottom: $measure-small;

    a {
        font-weight: $font-weight-bold;
    }

    svg {
        display: inline-block;
        vertical-align: bottom;
        margin-right: $measure-small;
        fill: $color-dove-gray;
        height: 20px;
    }
}

.socialmedia-card__message {
    color: $color-black;
    line-height: $line-height-default;
    word-wrap: break-word;
    margin-bottom: $measure-small;
}

.socialmedia-card__item-url {
    line-height: $line-height-default;
    word-wrap: break-word;
    margin-bottom: $measure-medium;
}

.socialmedia-card__timestamp,
.socialmedia-card__actions {
    display: inline-block;
    color: $color-dove-gray;
    font-size: $font-size-small;
    line-height: $line-height-default;
    margin-bottom: $measure-small;
}

.socialmedia-card__timestamp {
    margin-right: $measure-medium;

    &:after {
        content: '\2219';
        font-weight: $font-weight-bold;
        margin-left: $measure-medium;
    }
}

.socialmedia-card__actions {
    a {
        @include anchor(simple-image);
        margin-right: ($measure-small * 1.5);
    }
}
