////
/// Induction timetable
/// @group components
////
.accordion--induction {
    .accordion__content__heading-anchor.active {
        background: $color-hub;
    }

    .accordion__content__heading-anchor__label {
        font-size: $font-size-gamma;
        font-weight: $font-weight-bold;
    }
}

.induction__events__event {
    box-shadow: 0 0 1.5rem 3px rgba(0, 0, 0, 0.15);
    margin-bottom: $measure-medium;
    margin-right: $measure-large;
    padding: $measure-medium;

    @include media('<=medium') {
        margin-right: $measure-small;
    }

    .induction__events__details {
        @include grid;

        .induction__events__time,
        .induction__events__restriction {
            @include grid-column(6);
        }

        .induction__events__restriction {
            text-align: right;

            @include media('<=medium') {
                text-align: left;
            }
        }

        background: $color-hubsecondary;
        border-bottom: $border-width-default $border-style-default $color-black;
        color: $color-white;
        font-size: $font-size-delta;
        font-weight: $font-weight-semibold;
        margin-left: -#{$measure-medium};
        margin-right: -#{$measure-medium};
        margin-top: -#{$measure-medium};
        padding: $measure-small;
        width: calc(100% + #{$measure-large});
    }

    .induction__events__optional {
        background: $color-white-smoke;
        border-top: $border-width-default $border-style-default $color-quill;
        color: $color-black;
        font-size: $font-size-delta;
        font-weight: $font-weight-semibold;
        margin-left: -#{$measure-medium};
        margin-right: -#{$measure-medium};
        margin-bottom: -#{$measure-medium};
        padding: $measure-small;
        text-align: right;
        width: calc(100% + #{$measure-large});
    }

    .induction__events__facilitators {
        font-size: $font-size-small;
    }
}

.induction__header {
    @include grid;

    p {
        @include grid-column(6);
    }

    p + p {
        text-align: right;

        @include media('<=medium') {
            text-align: left;
        }

        span {
            font-weight: $font-weight-semibold;
        }
    }
}

.induction__event__content {
    @include grid-column(12);

    border-bottom: $border-width-hairline $border-style-default $color-gallery;
    padding-bottom: $measure-medium;
}

.induction__events__facilitator-list {
    @include grid-column(12);

    .induction__event__facilitator {
        @include grid;

        list-style-type: none;
        margin-bottom: $measure-medium;

        .induction__event__facilitator__portrait {
            @include grid-column(2);

            text-align: center;

            @include media('<=medium') {
                text-align: left;
            }

            .fa {
                background: $color-gallery;
                border-radius: 50%;
                color: $color-white;
                font-size: 3rem;
                height: 5.5rem;
                line-height: 5.5rem;
                text-align: center;
                width: 5.5rem;
            }

            img {
                border-radius: 50%;

                @include media('<=medium') {
                    max-width: 5.5rem;
                }
            }
        }

        .induction__event__facilitator__name {
            @include grid-column(10);

            font-family: $font-family-lato;
            font-size: $font-size-delta;
            font-weight: $font-weight-semibold;

            .induction__event__facilitator__jobtitle {
                font-family: $font-family-default;
                font-size: $font-size-default;
                font-weight: $font-weight-default;
            }
        }
    }
}

.induction__event__attendance {
    @include grid;

    font-family: $font-family-lato;
    font-size: $font-size-delta;
    font-weight: $font-weight-semibold;
    margin-bottom: $measure-medium;

    .induction__events__time,
    .induction__events__restriction {
        @include grid-column(6);
    }

    .induction__events__restriction {
        text-align: right;

        @include media('<=medium') {
            text-align: left;
        }
    }
}

.induction__events__optional {
    font-family: $font-family-lato;
    font-size: $font-size-delta;
    font-weight: $font-weight-semibold;
}

.induction__events__location,
.induction__event__location {
    .cta {
        max-width: 100%;

        span:first-child {
            display: inline-block;
            max-width: 90%;
        }
    }
}

.induction__event__location {
    @include grid-column(12);

    .induction__event__location__map {
        height: 320px;
        margin-bottom: $measure-medium;
        width: 100%;
    }
}

.induction__event__back {
    margin-bottom: $measure-medium;
    margin-top: $measure-medium;
}
