.connect-social__wrapper {
    @include grid-column-map(
        (
            'small': 12,
            'large': 10,
        )
    );
    @include grid-column-push-map(
        (
            'large': 1,
        )
    );

    @include media('<=medium') {
        margin: 0 15%;
    }
    @include media('<=small') {
        margin: 0;
    }
    @include media('>medium') {
        margin: 0;
    }
}

.connect-social__card {
    text-align: center;
    margin-top: $grid-padding;
    display: flex;
    align-items: stretch;

    @include grid-column(4);

    a {
        display: block;
        width: 100%;
        height: 100%;
        @include anchor(cta);
        @include anchor(cta--shadow);
        font-weight: $font-weight-medium;

        & > span {
            margin: $measure-large;
        }
    }
}

.connect-social__card--twitter {
    background-color: $color-twitter-background;
    width: 100%;
    span {
        color: $color-twitter-blue;
        font-size: $font-size-delta;
    }
}

.connect-social__card--fb {
    background-color: $color-white;
    width: 100%;
    span {
        color: $color-facebook-blue;
    }
}

.connect-social__card--twitter,
.connect-social__card--fb {
    a {
        padding: $grid-padding;
    }
}

.connect-social__card--insta {
    background-color: transparent;
    width: 100%;

    a {
        padding: 0;
    }

    img {
        width: 100%;
        height: 100%;
        position: relative;
    }
}

.connect-social__card__timestamp {
    color: $color-lucifer;
}

.connect-social__source {
    width: 100%;
    display: flex;
    flex-direction: row;
    justify-content: center;
    margin-top: 40px;

    & > div:first-child {
        margin-right: 30px;
        font-weight: $font-weight-semibold;

        .connect-social__source--twitter-icon,
        .connect-social__source--fb-icon {
            display: inline-block;
            margin: 0 5px 0 10px;
        }
        .connect-social__source--twitter-icon {
            color: $color-twitter-blue;
        }

        .connect-social__source--fb-icon {
            color: $color-facebook-blue;
        }
    }

    @include media('>medium') {
        margin-top: 60px;
        margin-bottom: 60px;
    }
}

.connect-social__card__ribbon {
    background-color: white;
    position: absolute;
    bottom: 0;
    color: $color-lucifer;
    padding: 10px;
}

.connect-social__card__ribbon__icon {
    margin: 0;
    color: $color-instagram;
    display: block;
    font-size: $font-size-gamma;
}

.connect-social__card__ribbon__text {
    margin: 0 0 8px;
    font-size: $font-size-default;
}

.connect-social__card__main-text {
    word-wrap: break-word;
}
