////
/// Content
/// @group layout
////

.content {
    @include grid-column(7);
    padding-left: 0;

    .sidebar + & {
        padding-left: $grid-padding;
        padding-right: 0;

        @include media('<=medium') {
            padding-left: 0;
        }
    }
}

.sidebar {
    @include grid-column(5);
    padding-left: 0;

    @include media('<=medium') {
        padding-right: 0;
    }

    .content + & {
        padding-left: $grid-padding;
        padding-right: 0;

        @include media('<=medium') {
            padding-left: 0;
        }
    }
}

.content--fixed {
    @extend .content;

    @include media('>medium') {
        width: 100%;
        padding-left: $fixed-sidebar-width;
        padding-right: 0;
    }
}

.sidebar--fixed {
    @extend .sidebar;

    @include media('<=medium') {
        padding-left: 0;
        padding-right: 0;
    }

    @include media('>medium') {
        width: $fixed-sidebar-width;
        float: left;
        padding-right: #{$grid-padding * 2};
        margin-left: -$fixed-sidebar-width;
    }
}
