.banner-stats {
    .banner-stats__items {
        @extend .grid--thirds;
        position: relative;

        @include media('>medium') {
            padding-top: $measure-large;
            padding-bottom: $measure-large;

            &:before {
                position: absolute;
                content: '';
                display: block;

                top: $measure-medium;
                left: 0;
                width: 100%;
                height: 1px;
                background: $color-orange;
            }
        }

        > div {
            position: relative;
            text-align: center;

            @include media('<=medium') {
                padding-top: $measure-medium;
                padding-bottom: $measure-medium;
            }

            img {
                display: inline-block;
                height: 100px;
            }

            div {
                color: $color-quill;
                font-weight: 600;
            }

            &:not(:first-child) {
                @include media('<=medium') {
                    display: none;
                }

                &:before {
                    position: absolute;
                    content: '';
                    display: block;

                    top: 0;
                    left: 0;
                    width: 1px;
                    height: 100%;
                    background: $color-orange;

                    @include media('<=medium') {
                        width: 100%;
                        height: 1px;
                    }
                }
            }
        }
    }

    &.banner-stats--open .banner-stats__items > div {
        display: block;
    }

    > div.banner-state__toggle {
        display: none;

        @include media('<=medium') {
            display: block;
        }

        margin-top: $measure-large;
        margin-bottom: $measure-large;
        text-align: center;

        a {
            @include anchor('white');
        }
    }
}
