.home__header {
    display: none;
}

.promo-card__wrapper {
    padding-bottom: 80px;
    position: relative;

    .promo-card {
        z-index: z-index(above);
    }

    .promo-card--left {
        @include media('>medium') {
            width: 320px;
            position: relative;
            float: right;
            top: -40%;
            right: 20%;
        }
    }

    .promo-card--right {
        position: relative;
        margin-top: 10px;
        @include media('>medium') {
            top: 60px;
            right: 10%;
            margin-top: 0;
        }
        padding-left: 0;
        padding-right: 0;

        @include media('>medium') {
            width: 500px;
        }
    }
}

@include media('>large') {
    .left-box,
    .right-box {
        margin-left: auto;
        margin-right: auto;
        max-width: $grid-width-extra-large;
        position: relative;
    }

    .left-box {
        &:before {
            background: $color-concrete;
            content: ' ';
            height: 100%;
            left: -50%;
            position: absolute;
            width: 100%;
        }
    }

    .right-box {
        &:before {
            background: $color-concrete;
            content: ' ';
            height: 100%;
            position: absolute;
            right: -50%;
            width: 100%;
        }
    }

    .section--padded.left-box,
    .section--padded.right-box {
        &:before {
            margin-top: -$measure-enormous;
        }
    }
}

.ksp__wrapper {
    margin-bottom: 80px;
    text-align: center;
}

.ksp__banner {
    @include media('>medium') {
        padding: 60px 0;
    }
}

.ksp__wrapper__anchor--more {
    @include anchor(skate);
    float: right;
    margin-top: 40px;
}
