@mixin anchor-primary-navigation() {
    @include anchor(reset);
    @include font-family(lato);
    color: $color-black;
    display: block;
    font-weight: $font-weight-bold;
    white-space: nowrap;

    @include media('<=large') {
        padding: $measure-medium $measure-small;
    }

    @include media('>large') {
        padding: $measure-large $measure-small;
        font-size: 18px; // TODO not px
    }

    span {
        position: relative;

        &:before {
            @include absolute(top, 100%);
            @include transition(opacity, transform);
            content: '';
            opacity: 0;
            background-color: $color-alizarin;
            width: 100%;
            height: $border-width-default;
            transform: translateX(-50%) translateY(100%);
        }
    }

    &:hover,
    &.active {
        span:before {
            opacity: 1;
            transform: translateX(-50%) translateY(0);
        }
    }

    @include anchor(block);
}

@mixin anchor-secondary-navigation() {
    @include anchor(reset);
    color: $color-black;
    display: block;
    padding: $measure-medium $measure-small;
    font-weight: $font-weight-bold;
    white-space: nowrap;

    &:before {
        @include absolute(bottom-full-offset-sides, $measure-small);
        @include transition(opacity, transform);
        content: '';
        opacity: 0;
        background-color: $color-alizarin;
        height: $border-width-default;
        transform: translateY(100%);
    }

    span {
        position: relative;

        &:before {
            @include absolute(full);
            content: attr(data-name);
            background-color: $color-lightgray;
            font-weight: $font-weight-default;
        }
    }

    &:hover,
    &.active {
        &:before {
            opacity: 1;
            transform: translateY(0);
        }

        span:before {
            opacity: 0;
        }
    }

    @include anchor(block);
}

@mixin anchor-tertiary-navigation() {
    @include anchor(reset);
    @include font-family(lato);
    color: $color-lucifer;
    display: block;
    padding: 0 $measure-small;
    font-size: $font-size-small;
    font-weight: $font-weight-bold;
    text-transform: uppercase;
    white-space: nowrap;

    &:focus {
        outline: $border-width-outline $border-style-default $color-black;
        outline-offset: $border-width-outline;
    }

    &:active {
        outline: $border-width-default $border-style-alternate $color-black;
