        @include absolute(bottom-left);
        content: '';
        opacity: 0;
        background-color: $color-white;
        width: 100%;
        height: $border-width-default;
        transition: opacity $transition-duration $transition-function
            $transition-duration;
        z-index: z-index(root);
    }

    &:hover {
        %heading-elements:before {
            @include animation(slide-left-in);
            opacity: 1;
            transition: none;
        }
    }

    &:focus {
        outline: $border-width-default $border-style-alternate $color-white;
        outline-offset: $border-width-outline;
    }

    &:active {
        outline: $border-width-default $border-style-alternate $color-white;
        outline-offset: 0;
    }
}

