.banner {
    position: relative;
}

.banner__container {
    @extend .container;
}

$banner-themes: (
    hub: (
        $color-black,
        $color-white,
        $color-hubgrey,
        $color-black,
    ),
);

.banner__content {
    @include grid-column-map(
        (
            'small': 12,
            'extralarge': 10,
        )
    );
    @include grid-column-push-map(
        (
            'small': 0,
            'extralarge': 1,
        )
    );

    .banner__content__promo {
        left: 0;

        @include media('>large') {
            width: 20%;
        }

        @include media('>medium', '<=large') {
            width: grid-column-width(3);
        }

        @include media('>between', '<=medium') {
            width: grid-column-width(4);
        }

        @include media('>small', '<=between') {
            width: grid-column-width(6);
        }

        @include media('<=small') {
            width: auto;
            margin-left: #{$measure-medium / 2};
            margin-right: #{$measure-medium / 2};
        }

        @include media('>medium') {
            position: absolute;
            bottom: -#{$measure-medium * 2};
            z-index: z-index(navigation);
        }

        @include media('<=medium') {
            bottom: $measure-medium;
        }

        .banner--cutout--left & {
            right: 0;
            left: auto;

            @include media('>small', '<=medium') {
                margin-left: auto;
            }
        }
    }

    .banner__content__text {
        padding-top: $measure-enormous;

        @include grid-column(6);

        @include media('>large', '<=extralarge') {
            left: grid-column-width(1);
        }
    }

    .banner__content__text__item {
        margin-bottom: $measure-small;
    }

    h1,
    .banner__content__text__subtitle {
        display: inline-block;
        margin: 0;
        padding: $measure-small $measure-medium;
        width: auto;
        font-size: $font-size-beta;
        @include font-family(lato);
    }

    .banner__content__text__subtitle {
        font-weight: $font-weight-default;
    }
    .banner__content__text__standfirst {
        font-weight: $font-weight-light;
    }

    .banner__content__text__standfirst {
        font-size: $font-size-gamma;
    }

    @include media('<=medium') {
        .banner__content__text__subtitle,
        .banner__content__text__standfirst {
            display: none;
        }
    }

    h1 {
        font-weight: $font-weight-heavy;
    }

    @each $theme, $colors in $banner-themes {
        $title-background: nth($colors, 1);
        $title-color: nth($colors, 2);
        $subtitle-background: nth($colors, 3);
        $subtitle-color: nth($colors, 4);

        .banner--theme--#{$theme} & {
            h1 {
                background-color: $title-background;
                color: $title-color;
            }
            .banner__content__text__subtitle {
                background-color: $subtitle-background;
                color: $subtitle-color;
            }
        }
    }
}

@each $theme, $colors in $banner-themes {
    $title-background: nth($colors, 1);
    $title-color: nth($colors, 2);
    $subtitle-background: nth($colors, 3);
    $subtitle-color: nth($colors, 4);

    .scp--#{$theme} {
        .banner--theme--hub {
            .banner__content {
                h1 {
                    background-color: $title-background;
                    color: $title-color;
                }
                .banner__content__text__subtitle {
                    background-color: $subtitle-background;
                    color: $subtitle-color;
                }
            }
        }
    }
}

// standfirst at bottom of banner:
.banner__standfirst {
    @extend .container;
}

.banner__standfirst__grid {
    @extend .grid;
}

.banner__standfirst__content {
    @include grid-column-map(
        (
            'small': 12,
            'medium': 10,
            'extralarge': 8,
        )
    );
    @include grid-column-push-map(
        (
            'small': 0,
            'medium': 1,
            'extralarge': 2,
        )
    );

    @include font-family(lato);
    font-size: $font-size-delta;
    color: $color-emperor;
    font-weight: $font-weight-light;
    text-align: center;
}

.banner__freehtml {
    position: relative;

    > div {
        z-index: z-index(above);
        @extend .container;

        > div {
            @extend .grid;

            > div {
                @include grid-column-map(
                    (
                        'small': 12,
                        'extralarge': 10,
                    )
                );
                @include grid-column-push-map(
                    (
                        'small': 0,
                        'extralarge': 1,
                    )
                );
            }
        }
    }

    &:after {
        position: absolute;
        display: block;
        content: '';
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        z-index: z-index(default);

        background: rgba(0, 0, 0, 0.8);

        @include media('>medium') {
            background: linear-gradient(
                rgba(0, 0, 0, 0),
                rgba(0, 0, 0, 0.8) $measure-large
            );
        }
    }
}
