/**
 * Pagination
 */
.pagination {
    width: 100%;
    margin: $measure-medium 0;
    text-align: center;
    display: inline-block;
    list-style: outside none none;
    line-height: 100%;
    font-weight: $font-weight-semibold;

    .previous,
    .number,
    .after,
    .ellipsis {
        padding: 4px 8px;
        margin: 10px 0 10px 5px;
        background-color: $color-gallery;
    }

    .previous,
    .number,
    .after {
        border: 1px solid transparent;
    }

    .number,
    .ellipsis {
        display: none;
    }

    .selected {
        display: inline;
        margin: 10px 0 10px 5px;
        color: $color-lucifer;
        border: 1px solid transparent;

        &:before {
            content: 'Page ';
        }
    }

    .previous {
        &:before {
            content: '< ';
        }
    }

    .after {
        &:after {
            content: ' >';
        }
    }

    a {
        text-decoration: none;
        color: $color-black;
        background-color: $color-gallery;
        border: 1px solid transparent;
        display: inline;

        &:link {
            color: $color-black;
        }
        &:visited {
            color: $color-black;
        }
        &:hover {
            color: $color-hub;
            background-color: $color-gallery;
            border: 1px solid $color-hub;
        }
        &:active,
        &:focus {
            color: $color-black;
            background-color: $color-hub;
            border: 1px solid transparent;
        }
    }

    .disabled {
        color: $color-lucifer;
        background-color: $color-gallery;
    }

    .ellipsis {
        background-color: transparent;
        margin: 0;
    }

    @include media('>small') {
        .ellipsis,
        .first-number,
        .last-number,
        .display {
            display: inline;
        }

        .number.extended {
            display: none;
        }

        .selected {
            border: 1px solid $color-lucifer;

            &:before {
                content: '';
            }
        }

        .previous {
            &:before {
                content: '';
            }
        }

        .after {
            &:after {
                content: '';
            }
        }
    }

    @include media('>small') {
        .number {
            display: inline;
        }
    }

    @include media('>medium') {
        .number.extended {
            display: inline;
        }
    }

    &.pagination--white-buttons {
        .previous,
        .number,
        .after {
            background-color: $color-white;
        }

        a {
            color: $color-black;
            background-color: $color-white;

            &:link {
                color: $color-black;
            }
            &:visited {
                color: $color-black;
            }
            &:hover {
                color: $color-hub;
                background-color: $color-white;
            }
        }

        .disabled {
            color: $color-lucifer;
            background-color: transparent;
        }
    }
}
