//
// to resize news images on homepage and maintain aspect ratio

.feature-news__wrapper {
    position: relative;
    padding-top: 66.25%; // A parameter of 66:25% creates an aspect ratio of 16:9
    overflow: hidden;

    @include media('<=between') {
        padding-top: inherit;
    }

    & img {
        position: absolute;
        top: 0;
        left: 0;

        @include media('<=between') {
            position: inherit;
        }
    }
}
.feature-news__details {
    background: $color-white;
    position: relative;
}
