.service-finder-nest {
    .service-finder__header {
        text-align: center;

        h2 {
            @include sr-only();
            margin-bottom: 0;
        }
    }

    .service-finder__top {
        min-height: 5rem;
    }

    .service-finder__search-form {
        margin-top: 1.5rem;
    }

    .service-finder__main {
        background-color: $color-gallery;
        padding-bottom: 0;
    }

    .service-finder__results {
        padding-left: $measure-large;
        padding-right: $measure-large;
        margin-top: $measure-small;
        margin-bottom: $measure-medium;
    }

    .service-finder__results__list {
        @include grid-top;
    }

    .service-finder-result {
        .service-finder-result__link {
            &,
            &:hover,
            &:active,
            &:focus {
                outline: $border-width-default $border-style-default $color-hub;
                outline-offset: 0;
            }
        }
    }

    .service-finder__results__count {
        margin-bottom: $measure-medium;
    }

    .query-box__label {
        color: $color-black;
        @include sr-only();
    }
}
