////
/// Footer
/// @group layout
////

.footer {
    background-color: $color-black;
    color: $color-white;
    padding-bottom: $measure-enormous;
    padding-top: $measure-medium;
}

.footer__wrapper {
    @include grid-top;
}

.footer__back-to-top {
    margin-bottom: 0;
    width: 100%;

    a {
        @include anchor(cta--hub--borderless);

        float: right;
        height: $measure-xlarge;
        padding: $measure-medium;
        width: $measure-xlarge;

        &:active {
            outline: $border-width-default $border-style-alternate $color-white;
            outline-offset: $border-width-outline;
        }

        &:focus {
            outline: $border-width-default $border-style-alternate $color-white;
            outline-offset: 0;
        }
    }

    span:not([class]) {
        @include visually-hidden;
    }

    .fa {
        padding-top: $border-width-default;
        border-top: $border-width-default $border-style-default $color-black;
    }
}

.footer__section {
    @include grid;

    text-align: center;

    @include media('>medium') {
        text-align: left;
    }

    a {
        @include anchor('white');
    }
}

.footer__section--second {
    border-top: $border-style-default $color-emperor $border-width-outline;
    padding-top: $measure-small;
}

.footer__institution,
.footer__site {
    a {
        @include anchor(skate--dark-bg);
    }

    font-family: $font-family-lato;
    font-size: $font-size-gamma;
    font-weight: $font-weight-heavy;
    margin-bottom: $measure-small;
}

.footer__site {
    font-weight: $font-weight-default;
}

.footer__address-details {
    display: block;
    font-size: $font-size-small;

    span {
        display: block;
        margin-bottom: $measure-small;
    }

    .footer__address-phone {
        @include media('>medium') {
            margin-top: $measure-large;
        }
    }

    a {
        @include anchor(skate);
    }
}

.footer__misc-links {
    margin-bottom: $measure-large;
    font-size: $font-size-small;

    li {
        display: inline;
        margin-bottom: $measure-small;

        @include media('>medium') {
            display: block;
        }
    }

    li + li {
        @include media('<=medium') {
            margin-left: $measure-medium;
        }
    }

    a {
        @include anchor(skate--dark-bg);
    }
}

.footer__social {
    li {
        display: inline-block;
        margin-bottom: $measure-small;
    }

    li + li {
        margin-left: $measure-medium;
    }

    a {
        @include anchor(simple-image);
        @include anchor(skate--dark-bg);
    }

    .fa {
        font-size: $font-size-gamma;
        vertical-align: top;
    }
}

.footer__logo {
    a {
        @include anchor(simple-image);
    }
}

.footer__social-hub {
    @include anchor(ghost);
    @include anchor('right-arrow');

    font-family: $font-family-lato;
    font-size: $font-size-delta;
    font-weight: $font-weight-heavy;
    margin: 0 0 $measure-medium;
    padding: 0;
}

.footer__social-heading {
    display: block;
    font-weight: $font-weight-semibold;
    margin-bottom: $measure-small;
}

.footer__copyright {
    margin-top: $measure-medium;

    @include media('>medium') {
        text-align: right;
        margin-top: 0;
    }

    a {
        @include anchor('skate--dark-bg');
    }

    &:first-of-type {
        margin-bottom: calc(
            #{$line-height-default}em - #{2 * $border-width-default}
        );
    }

    font-size: $font-size-small;

    span {
        display: block;
    }
}
