input.header-toggle-input {
    display: none;
}

.header-toggle {
    @include transition(background-color, color);
    display: inline-block;

    & > label {
        @include non-selectable;
    }

    input[type='checkbox']:checked + & {
        background-color: $color-black;
        color: $color-white;
        width: 100%;
        position: fixed;
        top: 0;
        left: 0;
        z-index: z-index(default);
    }
}

.header-toggle--menu .header-toggle__icon {
    @extend .fa !optional;
    @extend .fa-bars !optional;
}

.header-toggle--search .header-toggle__icon {
    @extend .fa !optional;
    @extend .fa-search !optional;
}

.header-toggle__icon {
    input[type='checkbox']:checked + .header-toggle & {
        @extend .fa-close !optional;
    }
}

.header-toggle__content {
    display: none;

    input[type='checkbox']:checked + .header-toggle & {
        display: block;
    }
}
