////
/// Nav Mobile
/// @group components
////

.utility-links {
    @include grid;
    left: grid-column-width(1);
    align-self: center;
    width: grid-column-width(3);

    @include media('<extralarge') {
        left: 0;
    }

    @include media('<medium') {
        display: none;
    }

    @include media('>between', '<medium') {
        width: grid-column-width(4);
    }

    .utility-links__list {
        .header:not(.header-v25) & {
            @include grid;
        }

        li {
            width: grid-column-width(4);

            padding-top: 0.75rem;
            text-align: center;
        }
    }

    span {
        display: block;

        &.fa {
            margin-bottom: $measure-small;
        }

        &:not(.fa) {
            font-family: $font-family-default;
            font-weight: $font-weight-semibold;
        }
    }

    a {
        @include anchor(reset);

        padding: 0;
        text-transform: uppercase;
    }
}

.utility-links-v25 {
    width: 100%;
    margin-top: $measure-medium + $measure-small;
    margin-left: 0;
    margin-right: 0;
    justify-content: center;

    .utility-links__list {
        gap: $measure-medium;
        margin-left: 0;
        margin-right: 0;
        display: flex;
        li {
            width: auto;
            padding-top: 0;

            li {
                padding: $measure-xsmall;
            }
        }

        &::before {
            content: none;
        }
    }
    .utility-links__item-icon {
        display: none;
        @include media('>large') {
            display: block;
        }
    }
    .utility-links__list-sub {
        background-color: $color-mine-shaft;
        z-index: 1;
        transform: translateY($measure-xsmall);
        a {
            @include anchor(skate--dark-bg);
        }
        li {
            text-align: left;
            border-bottom: 1px solid $color-grey;
            a {
                display: block;
                padding: $measure-xsmall $measure-medium-relative;
                width: 100%;
                min-width: max-content;
            }
        }
    }
    .utility-links__label {
        border-bottom: $border-width-default $border-style-default $transparent;
    }

    .utility-link-btn,
    .utility-link {
        display: flex;
        gap: $measure-small;
        align-items: center;
        text-transform: uppercase;
        padding: $measure-small;
        white-space: nowrap;
        @include anchor(outline--dark-bg);
        span {
            margin-bottom: 0;
        }

        & .utility-links__toggle-icon {
            margin-bottom: $measure-xsmall;
        }
    }
}
