////
/// Overlap stat
/// @group components
////

.overlap-stat__overlay {
    background-color: $color-orange;
    padding: $measure-large $measure-medium;
    font-weight: $font-weight-medium;
}

.overlap-stat__overlay--with-image {
    @include media('>medium') {
        padding: $measure-medium;
        z-index: z-index(above);
        position: relative;
        width: 45%;
        top: -7rem;
        margin-bottom: -7rem + $measure-medium;
    }

    @include media('>large') {
        width: 40%;
        top: -6rem;
        margin-bottom: -6rem + $measure-medium;
    }
}

.overlap-stat__lead {
    display: block;
    font-size: $font-size-beta;
    font-weight: $font-weight-semibold;
}

.overlap-stat__underlay {
    display: none;

    @include media('>medium') {
        display: inline-block;
        width: 100%;
        padding-left: 15%;
    }
}
