////
/// Text CTAs
/// @group components
////

/**
USED IN:
 - 361894 (DEV)

 */
.banner--text-ctas {
    @include grid;

    .banner__text {
        @include grid-column(6);

        position: relative;

        .banner__text__subtitle {
            @extend %alpha;
            @include font-family(lato);
            color: $color-emperor;
            margin-top: (-$measure-medium);
            margin-bottom: $measure-large;
            font-weight: $font-weight-semibold;
        }

        p {
            @include font-family(lato);
            color: $color-emperor;
            font-size: $font-size-delta;
            font-weight: $font-weight-light;
        }
    }

    .banner__ctas {
        padding-right: $grid-padding-half;

        @include media('>small') {
            margin-left: auto;
        }
        @include media('<=medium') {
            margin-right: auto;
        }

        @include media('<=small') {
            width: 100%;
            padding-left: $grid-padding-half;
        }
    }
}
