.site-search {
    .header:not(.header-v25) & {
        @include grid-column-base;
        @include grid-column(1);

        text-align: right;
        padding-right: $grid-padding-half;
        align-self: center;
    }
}

.header {
    &:not(.header-v25) {
        .site-search {
            left: grid-column-width(1);
            width: grid-column-width(1);
            padding-bottom: $measure-small;
            padding-top: $measure-medium;

            @include media('>min') {
                left: grid-column-width(1);
                width: grid-column-width(1);
            }

            @include media('>small') {
                left: grid-column-width(2);
                width: grid-column-width(2);
            }

            @include media('>between') {
                left: grid-column-width(1);
                width: 40px;
            }

            @include media('>medium') {
                left: grid-column-width(1);
            }

            @include media('>large') {
                left: grid-column-width(2);
            }

            @include media('>extralarge') {
                left: grid-column-width(3);
            }
        }
    }

    .site-search__form {
        @include media('<=medium') {
            top: calc(100% + #{$measure-small});
            right: -60%;
        }
    }
}

.site-search__container__toggle-label {
    @include anchor(outline--dark-bg);

    cursor: pointer;
    padding: $measure-medium;

    @include media('>medium') {
        padding: $measure-small;
    }

    &:before {
        @include font-family(icon);
        content: $fa-var-search;
        font-size: $font-size-gamma;
    }
}

.site-search__toggle,
.site-search__form {
    display: none;
}

.site-search__container {
    position: relative;
}

.site-search__form {
    position: absolute;
    right: 0;
    top: calc(100% + #{$measure-medium * 2.25});

    @include media('>small') {
        top: calc(100% + #{$measure-medium});
    }

    width: auto;
    height: $measure-xlarge;
    text-align: right;
    white-space: nowrap;
    z-index: z-index(navigation);
}

.site-search__button,
.site-search__input {
    vertical-align: top;
}

.site-search__input {
    font-family: $font-family-default;
    font-size: $font-size-default;
    font-weight: $font-weight-default;
    height: $measure-xlarge;
    margin: 0;
    padding: $measure-small;
    width: calc(#{$grid-width-min} - #{2 * $grid-padding + $measure-xlarge});

    &:focus {
        outline: $border-width-default $border-style-default $color-hub;
    }
}

.site-search__button {
    @extend .button--icon;
    width: $measure-xlarge;
    height: $measure-xlarge;

    span:before {
        @include font-family(icon);
        content: $fa-var-search;
        font-size: $font-size-gamma;
        font-weight: $font-weight-bold;
        line-height: $font-size-gamma;
    }
}

.header {
    .site-search__button {
        @include media('<=medium') {
            background: $color-hubgrey;
            color: $color-black;

            &:hover {
                background-color: darken($color-hubgrey, 6%);
            }
        }
    }
}

.site-search__toggle:checked + .site-search__container {
    .site-search__form {
        display: block;
    }

    .site-search__container__toggle-label:before {
        content: $fa-var-close;
    }
}

.search-page {
    margin-top: -#{$measure-medium * 2};

    .search-page__site-search {
        @include grid;
        min-height: 15.5rem;
    }

    .search-page__site-search__wrapper {
        @include grid;
        @include grid-column(6);
        @include grid-column-push(3);

        @include media('<=large') {
            @include grid-column(10);
            @include grid-column-push(1);
        }

        @include media('<=medium') {
            @include grid-column(12);
        }

        @supports (display: flex) {
            align-self: center;
        }
    }

    .site-search__label {
        color: $color-white;
        display: block;
        font-family: $font-family-lato;
        font-size: $font-size-default;
        font-weight: $font-weight-semibold;
        margin-left: $measure-small;
    }

    .site-search__form {
        width: 100%;
        display: block;
        margin-bottom: $measure-small;
        margin-left: $measure-small;
        margin-top: -2.5 * $measure-medium;
        position: relative;
        text-align: left;
        z-index: z-index(default);
    }

    .site-search__input {
        margin-left: $measure-small;
        width: grid-column-width(10);
    }
}

.search-results {
    background-color: $color-gallery;
    padding-left: 1rem;
}

.search-results__list {
    @include grid;
    @include grid-column(6);
    @include grid-column-push(3);

    @include media('<=large') {
        @include grid-column(10);
        @include grid-column-push(1);
    }

    @include media('<=medium') {
        @include grid-column(12);
    }
}

.search-result,
.best-bet {
    @include box-shadow(delta, rgba(0, 0, 0, 0.15));
    margin-bottom: $measure-medium;
    width: 100%;

    a {
        @include anchor(search-result);
    }

    &:hover,
    &:focus,
    &:active {
        box-shadow: 0 0 1.5rem 3px rgba(0, 0, 0, 0.15);
    }
}

.best-bet__title,
.search-result__title {
    font-size: $font-size-gamma;
    margin-bottom: $measure-small;
}

.search-result__url,
.best-bet__url {
    font-size: $font-size-small;
    font-style: italic;
}

.best-bet__label {
    color: $color-lucifer;
    font-family: $font-family-lato;
    font-size: $font-size-small;
    font-weight: $font-weight-heavy;
    position: absolute;
    right: $measure-medium;
    text-transform: uppercase;
    top: $measure-medium;
}

.search-results__count {
    margin-left: $measure-medium;
    margin-top: $measure-medium;

    span {
        font-weight: $font-weight-semibold;
    }
}
