////
/// Highlight info
/// @group components
////

.highlight-info-group {
    padding-top: $measure-medium;
    padding-bottom: $measure-large;
}

.highlight-info {
    padding-top: $measure-medium;
    padding-bottom: $measure-large;
    padding-right: $measure-medium;
}

.highlight-info__lead {
    display: block;
    margin-bottom: $measure-small;
    font-size: $font-size-beta;
    font-family: $font-family-lato;
    color: $color-flamingo;
}

.highlight-info__support {
    display: block;
    font-weight: $font-weight-semibold;
}

.highlight-info.highlight-info--prussian {
    .highlight-info__lead {
        color: $color-prussian;
    }
}
