////
/// Video.video-banner Base
/// @group components
////

.video-banner {
    list-style-type: none;
    margin-left: 0;
    margin-top: 0;
    position: relative;
}

.video-banner__banner {
    display: none;
    position: relative;

    &:before {
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        z-index: z-index(default);
        background-color: rgba(0, 0, 0, 0.5);
        opacity: 0;
        width: 100%;
        height: 96%; //refactor to remove magic numbers
        transition: opacity 1s;

        @include media('>small') {
            height: 98%; //refactor to remove magic numbers
        }

        @include media('>medium') {
            height: 99%; //refactor to remove magic numbers
        }
    }

    .video-banner__text__wrapper {
        opacity: 0;
        transition: opacity 2s;
    }
}

.video-banner__banner.playing {
    display: block;

    &.text,
    &.textout {
        &:before {
            opacity: 1;
        }
    }

    &.text .video-banner__text__wrapper {
        opacity: 1;
    }
}

.video-banner__text__wrapper {
    position: absolute;
    text-align: center;
    top: 0;
    width: 100%;
    z-index: z-index(above);

    .video-banner__text {
        @include transition(
            opacity,
            $transition-duration-long,
            $transition-function
        );

        display: block;
        margin-left: auto;
        margin-right: auto;
        width: 60%;

        .video-banner__text__title,
        .video-banner__text__subtitle {
            @extend %alpha;
            @include font-family(lato);
            color: $color-lightgrey;
            font-size: $font-size-alpha;
            font-weight: $font-weight-bold;
            line-height: $line-height-default;
            margin-bottom: $measure-large;
            margin-top: (-$measure-medium);
            text-shadow: 0 0 $border-width-hairline $color-black;
        }

        .video-banner__text__title {
            margin-bottom: 1rem;

            @include media('>large') {
                margin-top: 12%;
            }

            @include media('>medium', '<=large') {
                margin-top: 5%;
            }

            @include media('<=medium') {
                margin-top: 14%;
                font-size: $font-size-beta;
            }

            @include media('<=small') {
                font-size: $font-size-delta;
            }

            @include media('<=min') {
                font-size: $font-size-default;
            }
        }

        .video-banner__text__subtitle {
            font-size: $font-size-beta;

            @include media('<=medium') {
                font-size: $font-size-gamma;
                line-height: $line-height-small;
            }

            @include media('<=small') {
                font-size: $font-size-default;
            }
        }
    }
}

.video-banner__controls {
    display: inline-block;
    position: absolute;
    z-index: z-index(above);

    right: $measure-medium;
    bottom: $measure-medium;

    @include media('>medium') {
        right: $measure-large;
        bottom: $measure-large;
    }
}

.video-banner__play {
    @include anchor('cta');

    border: 0;
    background: white;

    padding: 0.25rem 0;
    width: 2rem;
    text-align: center;
    display: inline-block;

    span {
        @extend .fa;
        @extend .fa-pause;
    }

    &.video-banner__play--pause span {
        @extend .fa-play;
    }
}
