////
/// Animations
/// @group components
////

@each $direction in $animation-directions {
    @keyframes fade-#{$direction} {
        from {
            @if ($direction == in) {
                opacity: 0;
            } @else {
                opacity: 1;
            }
        }
        to {
            @if ($direction == in) {
                opacity: 1;
            } @else {
                opacity: 0;
            }
        }
    }
}

@each $direction in $animation-directions {
    @each $axis in $animation-axes-directions {
        @keyframes rotate-#{$axis}-#{$direction} {
            from {
                @if ($direction == in) {
                    @if ($axis == horizontal) {
                        transform: rotateY(-90deg);
                    } @else {
                        transform: rotateX(-90deg);
                    }
                } @else {
                    transform: none;
                }
            }
            to {
                @if ($direction == in) {
                    transform: none;
                } @else {
                    @if ($axis == horizontal) {
                        transform: rotateY(-90deg);
                    } @else {
                        transform: rotateX(-90deg);
                    }
                }
            }
        }
    }
}

@each $direction in $animation-directions {
    @each $axis in $animation-axes-directions {
        @keyframes scale-#{$axis}-#{$direction} {
            from {
                @if ($direction == in) {
                    @if ($axis == horizontal) {
                        transform: scaleY(0);
                    } @else {
                        transform: scaleX(0);
                    }
                } @else {
                    transform: none;
                }
            }
            to {
                @if ($direction == in) {
                    transform: none;
                } @else {
                    @if ($axis == horizontal) {
                        transform: scaleY(0);
                    } @else {
                        transform: scaleX(0);
                    }
                }
            }
        }
    }
}

@each $direction in $animation-directions {
    @each $slide, $value in $animation-cardinal-directions {
        @keyframes slide-#{$slide}-#{$direction} {
            from {
                @if ($direction == in) {
                    @if ($slide == top or $slide == bottom) {
                        transform: translateY(#{$value});
                    } @else {
                        transform: translateX(#{$value});
                    }
                } @else {
                    transform: none;
                }
            }
            to {
                @if ($direction == in) {
                    transform: none;
                } @else {
                    @if ($slide == top or $slide == bottom) {
                        transform: translateY(#{$value});
                    } @else {
                        transform: translateX(#{$value});
                    }
                }
            }
        }
    }
}

////
//Animation reveal
/////

$animation-duration: 1s;

.animation-reveal {
    background-color: transparent;
    width: 100%;
    height: auto;
    position: relative;

    &:after {
        content: '';
        position: absolute;
        top: 0;
        bottom: 0;
        animation-duration: $animation-duration;
        animation-timing-function: ease;
        animation-direction: both;
    }

    &:after {
        background-color: white;
        animation-delay: ($animation-duration / 3);
    }

    .animation-reveal__content {
        opacity: 0;
    }
}

.animation-reveal--shadow {
    @include media('>medium') {
        animation: box-shadow 0.5s ease-in ($animation-duration/1.2) forwards;
    }
}

.animation-reveal__load--right-to-left,
.animation-reveal__load--left-to-right {
    overflow: hidden;
    box-shadow: 0 0 1.5rem 3px rgba(0, 0, 0, 0);

    &:after {
        left: 0;
        right: 0;
        transform: translateX(100%);
    }

    .animation-reveal__content {
        animation: content-reveal 0s linear ($animation-duration/1.2) forwards;
    }

    &:after {
        animation-name: video-slide--left-to-right;
    }
}

.animation-reveal__load--left-to-right {
    &:after {
        animation-direction: reverse;
    }
}

@keyframes content-reveal {
    0% {
        opacity: 0;
    }
    100% {
        opacity: 1;
    }
}

@keyframes video-slide--left-to-right {
    0% {
        transform: translateX(100%);
    }
    50% {
        transform: translateX(0%);
    }
    100% {
        transform: translateX(-100%);
    }
}

@keyframes box-shadow {
    0% {
        box-shadow: 0 0 1.5rem 3px rgba(0, 0, 0, 0);
    }
    100% {
        box-shadow: 0 0 1.5rem 3px rgba(0, 0, 0, 0.15);
    }
}

@keyframes placehold-line {
    0%,
    99.999% {
        border-color: transparent;
    }
    100% {
        border-color: currentColor;
    }
}

@keyframes draw-line {
    0% {
        clip-path: polygon(
            $border-width-thick 0%,
            calc(100% - #{$border-width-thick}) 0%,
            calc(100% - #{$border-width-thick}) $border-width-thick,
            $border-width-thick $border-width-thick
        );
        transform: translateX(-100%);
    }
    25% {
        clip-path: polygon(
            $border-width-thick 0%,
            calc(100% - #{$border-width-thick}) 0%,
            calc(100% - #{$border-width-thick}) $border-width-thick,
            $border-width-thick $border-width-thick
        );
        transform: translateX(0);
    }
    25.001% {
        clip-path: polygon(
            calc(100% - #{$border-width-thick}) 0%,
            100% 0%,
            100% 100%,
            calc(100% - #{$border-width-thick}) 100%
        );
        transform: translateY(-100%);
    }
    50% {
        clip-path: polygon(
            calc(100% - #{$border-width-thick}) 0%,
            100% 0%,
            100% 100%,
            calc(100% - #{$border-width-thick}) 100%
        );
        transform: translateY(0);
    }
    50.001% {
        clip-path: polygon(
            0% calc(100% - #{$border-width-thick}),
            calc(100% - #{$border-width-thick})
                calc(100% - #{$border-width-thick}),
            calc(100% - #{$border-width-thick}) 100%,
            0 100%
        );
        transform: translateX(100%);
    }
    75% {
        clip-path: polygon(
            0% calc(100% - #{$border-width-thick}),
            calc(100% - #{$border-width-thick})
                calc(100% - #{$border-width-thick}),
            calc(100% - #{$border-width-thick}) 100%,
            0 100%
        );
        transform: translateX(0);
    }
    75.001% {
        clip-path: polygon(
            0% 0%,
            $border-width-thick 0%,
            $border-width-thick calc(100% - #{$border-width-thick}),
            0% calc(100% - #{$border-width-thick})
        );
        transform: translateY(100%);
    }
    99.999% {
        clip-path: polygon(
            0% 0%,
            $border-width-thick 0%,
            $border-width-thick calc(100% - #{$border-width-thick}),
            0% calc(100% - #{$border-width-thick})
        );
        transform: translateY(0);
    }
    100% {
        display: none;
    }
}

@keyframes maintain-line {
    0%,
    25% {
        clip-path: polygon(
            0% 0%,
            $border-width-thick 0%,
            $border-width-thick $border-width-thick,
            0% $border-width-thick
        );
    }
    25.001%,
    50% {
        clip-path: polygon(
            0% 0%,
            100% 0%,
            100% $border-width-thick,
            0% $border-width-thick
        );
    }
    50.001%,
    75% {
        clip-path: polygon(
            0% 0%,
            100% 0%,
            100% 100%,
            calc(100% - #{$border-width-thick}) 100%,
            calc(100% - #{$border-width-thick}) $border-width-thick,
            0% $border-width-thick
        );
    }
    75.001%,
    99.999% {
        clip-path: polygon(
            0% 0%,
            100% 0%,
            100% 100%,
            0% 100%,
            0% calc(100% - #{$border-width-thick}),
            calc(100% - #{$border-width-thick})
                calc(100% - #{$border-width-thick}),
            calc(100% - #{$border-width-thick}) $border-width-thick,
            0% $border-width-thick
        );
    }
    100% {
        display: none;
    }
}
