////
/// Quick links
/// @group components
////

.quick-links {
    @include grid;
    padding-bottom: $measure-small;
    padding-left: $measure-medium + $measure-small;
    padding-right: $measure-medium + $measure-small;
    padding-top: $measure-medium;

    @include media('<=medium') {
        padding-left: $measure-medium + $measure-small;
        padding-right: $measure-medium + $measure-small;
    }

    .quick-links__list {
        @include grid-top;
    }

    .quick-link {
        margin-right: $measure-medium;
        padding-bottom: $measure-medium;
        width: 100%;

        @include media('>between') {
            width: calc(#{50%} - #{$measure-medium});
        }

        @include media('>medium') {
            width: calc(#{33%} - #{$measure-medium});
        }

        @include media('>large') {
            width: calc(#{25%} - #{$measure-medium});
        }

        a {
            @include anchor(quick-link);
            font-weight: $font-weight-bold;
            white-space: initial;

            display: flex;

            span:not([class]) {
                text-wrap: wrap;
                border-bottom: initial;
                text-decoration: underline;
            }
        }

        .fa {
            display: inline-block;
            padding-left: $measure-small;
            padding-right: $measure-medium;
            text-align: center;
            width: $measure-large;
        }
    }
}
