////
/// Media
/// @group components
////

.media,
.video-preview {
    width: 100%;
    height: auto;
    margin-bottom: $measure-medium;
    overflow: hidden;
    position: relative;

    // by default, assume 16:9
    &:not([class*='media--']) {
        @extend %media--16-by-9;
    }

    figcaption {
        z-index: z-index(above);
    }
}

.media %media-elements,
.media %image-elements,
.video-preview %media-elements,
.video-preview %image-elements,
.video-preview a {
    @include absolute(top-full);
    height: 100%;
    // necessary for `iframe`
    margin-bottom: 0;
}

.media--vimeo,
.media--youtube {
    @extend %media--16-by-9;
}

// Build aspect ratio classes
@each $percentage, $ratio in $ratios {
    %img--#{nth($ratio, 1)}-by-#{nth($ratio, 2)} {
        height: $percentage;
    }
    %media--#{nth($ratio, 1)}-by-#{nth($ratio, 2)} {
        padding-top: $percentage;
    }
}

.video-preview {
    %media-elements,
    %image-elements {
        z-index: z-index(default);
    }

    a {
        @include anchor(video-preview);
        z-index: z-index(above);
    }
}

.video-preview--loading {
    %media-elements,
    %image-elements,
    a {
        @include visually-hidden;
    }
}

.media-block {
    figure,
    .video-preview,
    .media {
        margin-bottom: 0;
    }

    figcaption {
        display: inline-block;
        width: 100%;
        padding-top: $measure-small;
        color: $color-lucifer;
        font-style: normal;
        font-family: $font-family-default;
        font-weight: $font-weight-default;

        .figcaption__bulleted-item {
            text-transform: uppercase;
        }

        .figcaption__bulleted-item:not(:first-of-type) {
            &:before {
                content: '\25CF';
                padding-left: $measure-small;
                padding-right: $measure-small;
            }
        }

        span {
            white-space: nowrap;
        }

        p:not(:first-child) {
            margin-top: $measure-small;
        }
    }

    .video-preview__button {
        &:before {
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
            -webkit-transform: translate(-50%, -50%);
            @extend .fa;
            @extend .fa-play-circle;
            font-size: 100px;
            color: $color-white;
            opacity: $opacity-half;
            z-index: z-index(above);
        }

        &:hover:before {
            opacity: $opacity-shade;
        }
    }

    .media-block__wrapper {
        width: 100%;
    }
}
