////
/// Tertiary Navigation
/// @group components
////

.tertiary-navigation--desktop {
    position: absolute;
    bottom: 100%;
    right: 0;

    @include media('<=medium') {
        display: none;
    }

    .tertiary-navigation__list {
        @include reset-font-size('li');

        li {
            display: inline-block;
        }

        a {
            @include anchor(tertiary-navigation);
        }
    }
}

.tertiary-navigation--mobile {
    .tertiary-navigation__list {
        background-color: $color-black;
        padding: $measure-small 0;
        margin-top: $border-width-thick;
        position: relative;
        text-align: left;

        &:before {
            content: '';
            background-color: $color-hub;
            width: 50%;
            max-width: 5rem;
            height: $border-width-thick;
            position: absolute;
            bottom: 100%;
            left: 0;
        }

        li {
            display: block;
        }

        a {
            @include anchor(reset);
            @include anchor(nav-mobile-link);
            color: $color-hubgrey;
            font-weight: $font-weight-bold;
            text-transform: uppercase;
        }
    }
}

.tertiary-navigation__toggles {
    @include media('>medium') {
        display: none;
    }
}
