.news__img {
    margin: 0 auto $measure-medium;
}

.news__published {
    font-style: italic;
    margin-bottom: $measure-medium;
}

.student-news-finder {
    margin-top: -#{$measure-medium * 2};

    .student-news-finder__wrapper {
        @include grid;
        min-height: 15.5rem;
    }
}

.student-news-finder {
    overflow-x: hidden;

    .student-news-finder__search-form {
        @include grid-column(6);
        @include grid-column-push(3);

        @include media('<=large') {
            @include grid-column(10);
            @include grid-column-push(1);
        }

        @include media('<=medium') {
            @include grid-column(12);
        }

        @supports (display: flex) {
            align-self: center;
        }
    }

    .student-news-finder__top {
        @include grid;
        min-height: 10.5rem;

        @include media('<=medium') {
            margin-left: $measure-small;
            margin-right: $measure-small;
        }
    }

    .student-news-finder__search-form {
        display: block;
        margin: 3.5rem $measure-small 0;
        position: relative;
        text-align: left;
    }

    .query-box__label {
        color: $color-white;
        display: block;
        font-family: $font-family-lato;
        font-size: $font-size-default;
        font-weight: $font-weight-semibold;
        margin-left: $measure-small;
    }

    .query-box__query {
        font-family: $font-family-default;
        font-size: $font-size-default;
        font-weight: $font-weight-default;
        height: $measure-xlarge;
        margin: 0 0 0 $measure-small;
        padding: $measure-small;
        vertical-align: top;
        width: grid-column-width(10);

        @include media('<=min') {
            width: grid-column-width(9);
        }

        &:focus {
            outline: $border-width-default $border-style-default $color-hub;
        }
    }

    .query-box__button {
        @extend .button--icon;
        width: $measure-xlarge;
        height: $measure-xlarge;
        vertical-align: top;

        span:before {
            @include font-family(icon);
            content: $fa-var-search;
            font-size: $font-size-gamma;
            font-weight: $font-weight-bold;
            line-height: $font-size-gamma;
        }
    }

    .student-news-finder__main {
        @include grid;
        background-color: $color-gallery;
        padding-bottom: $measure-large;

        @include media('<=medium') {
            margin-left: $measure-small;
            margin-right: $measure-small;
        }
    }

    .student-news-finder__main__left {
        @include grid-column(2);
        @include grid-column-push(1);
    }

    .student-news-finder__main__right {
        @include grid-column(7);
        @include grid-column-push(2);
    }

    .student-news-finder__results__count {
        margin-bottom: $measure-xlarge;
        padding-left: $measure-medium;
        text-align: center;
        width: 100%;

        span span {
            font-weight: $font-weight-semibold;
        }
    }

    .student-news-finder__results__refresh {
        display: inline-block;
        color: $color-hub;
        margin-left: $measure-small;
    }

    .fade-out {
        opacity: 0;
        pointer-events: none;
        user-select: none;
        animation: fade-out 1s linear;
    }

    .student-news-finder__results {
        @include grid;
        padding-left: $measure-medium + $measure-small;
        padding-right: $measure-medium + $measure-small;

        @include media('<=medium') {
            padding-left: $measure-medium + $measure-small;
            padding-right: $measure-medium + $measure-small;
        }
    }

    .student-news-finder__results__list {
        @include grid;
    }

    .student-news-finder__card {
        width: 100%;

        @include media('>between') {
            width: calc(#{grid-column-width(6) - #{$measure-large}});
            margin-right: $measure-large;
        }
    }

    .student-news-finder__card {
        img {
            width: 100%;
        }

        .student-news-finder__details {
            padding: $measure-medium;
            text-align: center;
            width: 100%;
        }
    }

    .student-news-finder__card__title {
        @include anchor(quick-link--feature);

        font-family: $font-family-lato;
        font-size: $font-size-delta;
        font-weight: $font-weight-heavy;
        white-space: normal;
    }

    .student-news-finder__card__published {
        margin-top: $measure-medium;

        @include media('<=between') {
            margin-top: $measure-small;
        }
    }

    .student-news-finder__card__category {
        @include anchor('purple');
        font-size: $font-size-small;
        font-weight: $font-weight-semibold;
    }

    .item__date {
        font-size: $font-size-small;
    }
}
