////
/// Functions
/// @group abstracts
////

///
/// Contrasting Text Color
///
/// @param {Color} $background-color - Background Color to contrast
/// @param {Color} $text-color-light [$color-white] - Color for dark backgrounds
/// @param {Color} $text-color-dark [$color-black] - Color for light backgrounds
///
/// @example scss
///   contrasting-color(#123123)      => #ffffff
///   contrasting-color($color-quill) => #222222
///
/// @throw Error if no parameter is passed.
///
/// @return {Color} Contrasting Text Color
///
@function contrasting-color(
    $background-color,
    $text-color-dark: $color-black,
    $text-color-light: $color-white
) {
    @if not $background-color {
        @warn "contrasting-color() expects at least one parameter.";
        @return false;
    }

    @if lightness($background-color) > 50 {
        @return $text-color-dark;
    }

    @return $text-color-light;
}

///
/// Accepts two or three parameters.
///
/// @param {Color} $from - Start Color
/// @param {Color} $to - End Color
/// @param {String} $direction [to bottom] - Gradient direction
///
/// @example scss - Two parameters
///   background-image: gradient(white, black);
///   // background-image: linear-gradient(to bottom, white, black);
///
/// @example scss - Three parameters
///   background-image: gradient(to top right, black, white);
///   // background-image: linear-gradient(to top right, black 0%, white 100%);
///
/// @throw Error if two or more parameters are not passed.
///
/// @return {String} Linear Gradient snippet
///
@function gradient($direction, $color-stops...) {
    @if not $direction and not $color-stops {
        @warn "gradient() expects a direction and two or more colors to gradient between.";
        @return false;
    }

    @if not is-direction($direction) {
        $color-stops: $direction, $color-stops;
        $direction: 'to bottom'; // equivalent to "to bottom"
    }

    @return linear-gradient(#{$direction}, $color-stops);
}

///
/// Test if `$value` is a valid direction
///
/// @param {*} $value - Value to test
///
/// @return {Bool}
///
@function is-direction($value) {
    $is-keyword: index(
        (
            to top,
            to top right,
            to right top,
            to right,
            to bottom right,
            to right bottom,
            to bottom,
            to bottom left,
            to left bottom,
            to left,
            to left top,
            to top left
        ),
        $value
    );
    $is-angle: type-of($value) == 'number' and
        index('deg' 'grad' 'turn' 'rad', unit($value));

    @return $is-keyword or $is-angle;
}

///
/// Test if `$value` is a valid event
///
/// @param {*} $value - Value to test
///
/// @return {Bool}
///
@function is-event($value) {
    $is-event: type-of($value) == 'string' and str-index($value, ':');

    @return $is-event;
}

///
/// Test if `$value` is a valid class
///
/// @param {*} $value - Value to test
///
/// @return {Bool}
///
@function is-class($value) {
    $is-class: type-of($value) == 'string' and str-index($value, '.');

    @return $is-class;
}

///
/// Test if `$value` is a valid corner or side
///
/// @param {*} $value - Value to test
///
/// @return {Bool}
///
@function is-edge($value) {
    $is-side: index((top, right, bottom, left), $value);
    $is-corner: index((top-left, top-right, bottom-right, bottom-left), $value);

    @return $is-side or $is-corner;
}

///
/// Test if `$value` is a valid length
///
/// @param {*} $value - Value to test
///
/// @return {Bool}
///
@function is-length($value) {
    $is-length: type-of($value) == 'number' and
        index(
            '%' 'ch' 'cm' 'em' 'ex' 'in' 'mm' 'pc' 'pt' 'px' 'rem' 'vmax' 'vmin'
                'vh' 'vw',
            unit($value)
        );

    @return $is-length;
}

///
/// Test if `$value` is a valid class
///
/// @param {*} $value - Value to test
///
/// @return {Bool}
///
@function is-boolean($value) {
    @if (
        $value ==
            false or
            $value ==
            'false' or
            $value ==
            true or
            $value ==
            'true'
    ) {
        @return true;
    }

    @return false;
}

///
/// Define Opacity
///
/// @param {String} $value - Label for the target opacity
///
/// @require {Variable} $opacities
///
/// @example scss
///   opacity("shade") => 0.850
///   opacity("tint")  => 0.100
///
/// @throw Error if no parameters are passed.
/// @throw Error if parameter passed does not match a key in $opacities.
///
/// @return {Number} Opacity value
///
@function opacity($value) {
    @if not $value {
        @warn "opacity() expects one parameter.";
        @return false;
    }

    @if not map-has-key($opacities, $value) {
        @warn "There is no value named `#{$value}` in `$opacities`. Value should be one of `#{map-keys($opacities)}`.";
        @return false;
    }

    @return map-get($opacities, $value);
}

///
/// Strip Units from a Number
///
/// @param {Number} $value - Number with units
///
/// @example scss
///   strip-units(2em)  => 2
///   strip-units(37px) => 37
///
/// @return {Number} Number without units
///
@function strip-units($value) {
    @if not $value {
        @warn "strip-units() expects at least one parameter.";
        @return false;
    }

    @return $value / ($value * 0 + 1);
}

///
/// Unpack Transition List
///
/// @param {list} $list - Transition list to unpack
///
/// @example scss
///   unpack-transition(all 1s)            => all 1s ease
///   unpack-transition(opacity 2s linear) => opacity 2s linear
///
/// @see {Mixin} transition
///
/// @return {String} Full Transition
///
@function unpack-transition($list) {
    $property: nth($list, 1);
    $time: if(length($list) > 1, nth($list, 2), $transition-duration);
    $function: if(length($list) > 2, nth($list, 3), $transition-function);
    $delay: if(length($list) > 3, nth($list, 4), 0s);

    @return $property $time $function $delay;
}

///
/// Unpack Animation List
///
/// @param {list} $list - Aniamtion list to unpack
///
/// @see {Mixin} animation
///
/// @return {String} Full Animation
///
@function unpack-animation($list) {
    $name: nth($list, 1);
    $duration: if(length($list) > 1, nth($list, 2), $transition-duration);
    $function: if(length($list) > 2, nth($list, 3), $transition-function);
    $delay: if(length($list) > 3, nth($list, 4), 0s);
    $fill-mode: if(length($list) > 4, nth($list, 5), both);
    $direction: if(length($list) > 4, nth($list, 6), normal);

    @return $name $duration $function $delay $fill-mode $direction;
}

///
/// Walk through a List with a Function
///
/// @param {List}    $list - List to walk through
/// @param {String}  $function - Function to apply to all members
/// @param {ArgList} $args - Extra arguments to pass to the function
///
/// @example scss
///   walk("one two three", to-upper-case) => "ONE TWO THREE"
///
/// @return {List} Updated list
///
@function walk($list, $function, $args...) {
    @if not function-exists($function) {
        @warn "There is no function named `#{$function}`.";
        @return false;
    }

    @for $i from 1 through length($list) {
        $list: set-nth($list, $i, call($function, nth($list, $i), $args...));
    }

    @return $list;
}

///
/// Simplify z-index.
///
/// @param {String} $value - Generic level
///
/// @require {Variable} $z-indexes
///
/// @example scss
///   z-index(above)        => 2
///   z-index(notification) => 5
///
/// @throw Error if no parameters are passed.
/// @throw Error if parameter passed does not match a key in $z-indexes.
///
/// @return {Number} z-index
///
@function z-index($z-index) {
    @if not $z-index {
        @warn "z-index() expects at least one parameter.";
        @return false;
    }

    @if not map-has-key($z-indexes, $z-index) {
        @warn "There is no `z-index` named `#{$z-index}` in `$z-indexes`. `z-index` should be one of `#{map-keys($z-index)}`.";
        @return false;
    }

    @return map-get($z-indexes, $z-index);
}

///
/// Simplify box-shadow
///
@function box-shadow($box-shadow) {
    @if not $box-shadow {
        @warn "box-shadow() expects at least one parameter.";
        @return false;
    }

    @if not map-has-key($box-shadows, $box-shadow) {
        @warn "There is no `box-shadow` named `#{$box-shadow}` in `$box-shadows`. `box-shadow` should be one of `#{map-keys($box-shadows)}`.";
        @return false;
    }

    @return map-get($box-shadows, $box-shadow);
}

// stylelint-disable max-nesting-depth
///
/// Unique Groups
///
/// @param {List|Map} $data - Data in List/Map format
///
/// @example scss
///   $list:
///     alpha,
///     beta,
///     gamma;
///   @each $unique-group in unique-groups($list) {
///     @each $item in $unique-group {
///       .from-#{nth($item, 1)}-to-#{nth($item, 2)} {
///         color: initial;
///       }
///     }
///   }
///   // .from-alpha-to-beta {
///   //   color: initial;
///   // }
///   // .from-alpha-to-gamma {
///   //   color: initial;
///   // }
///   // .from-beta-to-gamma {
///   //   color: initial;
///   // }
///
/// @throw Error if no parameter is passed.
/// @throw Error if parameter passed is neither a List nor a Map.
///
/// @return {Map} Unique groups
///
@function unique-groups($data, $size: 2) {
    @if not $data or not (type-of($data) == list or type-of($data) == map) {
        @warn "unique-groups() expects either a single List or single Map of data.";
        @return false;
    }

    $unique-groups: ();

    @if type-of($data) == list {
        $seen-first: ();
        @each $first in $data {
            $seen-first: append($seen-first, $first);
            $seen-second: ();
            @each $second in $data {
                $seen-second: append($seen-second, $second);
                @if $first != $second and not index($seen-first, $second) {
                    @if $size >= 3 {
                        @each $third in $data {
                            @if $second !=
                                $third and not
                                index($seen-second, $third)
                            {
                                $unique-group: ($first, $second, $third);
                                $unique-groups: append(
                                    $unique-groups,
                                    $unique-group
                                );
                            }
                        }
                    } @else {
                        $unique-group: ($first, $second);
                        $unique-groups: append($unique-groups, $unique-group);
                    }
                }
            }
        }
    } @else if type-of($data) == map {
        $seen-first: ();
        @each $first-key, $first-value in $data {
            $seen-first: append($seen-first, $first-key);
            $seen-second: ();
            @each $second-key, $second-value in $data {
                $seen-second: append($seen-second, $second-key);
                @if $first-key !=
                    $second-key and not
                    index($seen-first, $second-key)
                {
                    @if $size >= 3 {
                        @each $third-key, $third-value in $data {
                            @if $second-key !=
                                $third-key and not
                                index($seen-second, $third-key)
                            {
                                $unique-group: (
                                    (
                                        $first-key: $first-value,
                                    ),
                                    (
                                        $second-key: $second-value,
                                    ),
                                    (
                                        $third-key: $third-value,
                                    )
                                );
                                $unique-groups: append(
                                    $unique-groups,
                                    $unique-group
                                );
                            }
                        }
                    } @else {
                        $unique-group: (
                            (
                                $first-key: $first-value,
                            ),
                            (
                                $second-key: $second-value,
                            )
                        );
                        $unique-groups: append($unique-groups, $unique-group);
                    }
                }
            }
        }
    } @else {
        @warn "unique-groups() expects either a List or Map.";
        @return false;
    }

    @return $unique-groups;
}
// stylelint-enable max-nesting-depth

///
/// Round a number to specified digits.
///
/// @param {Number} $number A number to round
/// @param {Number} [$digits:0] Digits to output
/// @param {String} [$mode:round] (round|ceil|floor) How to round a number
///
/// @example
///   decimal-round(0.333)    => 0
///   decimal-round(0.333, 1) => 0.3
///   decimal-round(0.333, 2) => 0.33
///   decimal-round(0.666)    => 1
///   decimal-round(0.666, 1) => 0.7
///   decimal-round(0.666, 2) => 0.67
///
/// @return {Number} A rounded number
///
@function decimal-round($number, $digits: 0, $mode: round) {
    $n: 1;

    // $number must be a number
    @if type-of($number) != number {
        @warn "#{$number} is not a number.";
        @return $number;
    }

    // $digits must be a unitless number
    @if type-of($digits) != number {
        @warn "#{$digits} is not a number.";
        @return $number;
    } @else if not unitless($digits) {
        @warn "#{$digits} has a unit.";
        @return $number;
    }

    @for $i from 1 through $digits {
        $n: $n * 10;
    }

    @if $mode == round {
        @return round($number * $n) / $n;
    } @else if $mode == ceil {
        @return ceil($number * $n) / $n;
    } @else if $mode == floor {
        @return floor($number * $n) / $n;
    } @else {
        @warn "#{$mode} is undefined keyword.";
        @return $number;
    }
}

///
/// Ceil a number to specified digits.
///
/// @param {Number} $number A number to round
/// @param {Number} [$digits:0] Digits to output
///
/// @example
///   decimal-ceil(0.333)    => 1
///   decimal-ceil(0.333, 1) => 0.4
///   decimal-ceil(0.333, 2) => 0.34
///   decimal-ceil(0.666)    => 1
///   decimal-ceil(0.666, 1) => 0.7
///   decimal-ceil(0.666, 2) => 0.67
///
/// @return {Number} A ceiled number
///
@function decimal-ceil($number, $digits: 0) {
    @return decimal-round($number, $digits, ceil);
}

///
/// Floor a number to specified digits.
///
/// @param {Number} $number A number to round
/// @param {Number} [$digits:0] Digits to output
///
/// @example
///   decimal-floor(0.333)    => 0
///   decimal-floor(0.333, 1) => 0.3
///   decimal-floor(0.333, 2) => 0.33
///   decimal-floor(0.666)    => 0
///   decimal-floor(0.666, 1) => 0.6
///   decimal-floor(0.666, 2) => 0.66
///
/// @return {Number} A floored number
///
@function decimal-floor($number, $digits: 0) {
    @return decimal-round($number, $digits, floor);
}

///
/// Accepts 2 or 3 parameters.
///
/// @param {String} $direction [to bottom] - Gradient direction
/// @param {List} $color-stops - List of Color values
///
/// @throw Error if 2 or 3 parameters are not passed.
///
/// @return {String} Scrim Gradient snippet
///
@function scrim-gradient($direction, $color-stops...) {
    @if not $direction and not $color-stops {
        @warn "`scrim-gradient()` expects a direction and/or two colors to gradient between.";
        @return false;
    }

    @if not is-direction($direction) {
        $color-stops: join($direction, $color-stops);
        $direction: 'to bottom';
    }

    $start-color: nth($color-stops, 1);
    $end-color: nth($color-stops, 2);

    @return linear-gradient(
        #{$direction},
        mix($start-color, $end-color, 100%) 0%,
        mix($start-color, $end-color, 73.8%) 19%,
        mix($start-color, $end-color, 54.1%) 34%,
        mix($start-color, $end-color, 38.2%) 47%,
        mix($start-color, $end-color, 27.8%) 56.5%,
        mix($start-color, $end-color, 19.4%) 65%,
        mix($start-color, $end-color, 12.6%) 73%,
        mix($start-color, $end-color, 7.5%) 80.2%,
        mix($start-color, $end-color, 4.2%) 86.1%,
        mix($start-color, $end-color, 2.1%) 91%,
        mix($start-color, $end-color, 0.8%) 95.2%,
        mix($start-color, $end-color, 0.2%) 98.2%,
        mix($start-color, $end-color, 0%) 100%
    );
}

///
/// PX to REM
///
/// @param {Number} $pixel-value - Pixel Value
///
/// @example scss
///   rem(24px) => 1.5rem
///
/// @throw Error if no value is passed.
/// @throw Error if value is either missing units or has the wrong type.
///
/// @return {Number} Rem Value
///
@function rem($pixel-value) {
    @if not $pixel-value or unit($pixel-value) != 'px' {
        @warn "rem() expects a single parameter with pixel units.";
        @return false;
    }

    @return $pixel-value / $base-font-size * 1rem;
}

///
/// REM to PX
///
/// @param {Number} $rem-value - Rem Value
///
/// @example scss
///   px(1.5rem) => 1.5px
///
/// @throw Error if no value is passed.
/// @throw Error if value is either missing units or has the wrong type.
///
/// @return {Number} Pixel Value
///
@function px($rem-value) {
    @if not $rem-value {
        @warn "px() expects a single parameter with rem units.";
        @return false;
    }

    @return ($rem-value / 1rem * $base-font-size);
}

///
/// Grid Column Width
///
/// @param {Number} $value - Size/span of the column
///
/// @require {Variable} $grid-columns
///
/// @example scss
///   width: grid-column-width(5);
///   // width: 41.667%;
///
/// @throw Error if no parameter is passed.
/// @throw Error if parameter passed is not within the limit of `$grid-columns`.
///
/// @return {Length} width
///
@function grid-column-width($value) {
    @if not $value {
        @warn "grid-column() expects one parameter.";
        @return false;
    }

    @if $value > $grid-columns {
        @warn "`#{$value}` is not less than or equal to `$grid-columns`.";
        @return false;
    }

    @return decimal-round(($value / $grid-columns) * 100%, 3);
}

///
/// Asset URL
///
/// @param {$asset} $string - asset path from /main/ in server
///
/// @return {String} absolute url of asset
///
@function asset-url($asset) {
    @return unquote(
        'url(/__data/assets/git_bridge/0013/412600/main' + $asset + ')'
    );
}
