.home-ctas {
    @include grid;

    background-color: $color-gallery;
    padding-left: $measure-medium + $measure-small;
    padding-right: $measure-medium + $measure-small;
    padding-bottom: $measure-xlarge;

    @include media('<=medium') {
        padding-left: $measure-medium + $measure-small;
        padding-right: $measure-medium + $measure-small;
    }

    .cta-group {
        @include grid-top;
    }
}
