////
/// Related course
/// @group components
////

.related-course {
    width: 100%;
    border-bottom: $border-width-default $border-style-default $color-gallery;

    &:first-child {
        border-top: $border-width-default $border-style-default $color-gallery;
    }

    .fa {
        display: inline-block;
        vertical-align: middle;
    }

    a {
        @include anchor(railtrack);
        display: block;
        padding-top: $measure-small;
        padding-bottom: $measure-small;
    }
}

.related-course__title {
    width: 60%;
    display: inline-block;
    vertical-align: middle;
    padding-right: $measure-medium;
}

.related-course__qualification {
    width: 30%;
    display: inline-block;
    vertical-align: middle;
}
