////
/// Anchors
/// @group base
////

a {
    @include anchor(inline);
    position: relative;

    // hide extra content, text targetted only to screen readers
    // e.g. <a href="#">Continue reading <span>this News Article</span></a>
    // & > span:not([class]):not([itemprop]) {
    //     @include visually-hidden;
    // }
}

%heading-elements > a {
    @include anchor(heading);
}

.dark-background a {
    @include anchor(dark-background);
}

.ghost {
    @include anchor(ghost);
}

// Skip Links
.skip {
    @include anchor(skip);
}

.draw-outline {
    @include anchor(draw-outline, white);
}

.simple-image {
    @include anchor('simple-image');
}
