#managed-form-container {
    width: 100%;
}

.employability-finder__form {
    width: 100%;

    .filter-box__inner {
        padding: initial;

        @include media('>between') {
            h2 {
                display: inline;
            }
        }

        .fa-sliders {
            font-size: $font-size-gamma;
            @include media('<=between') {
                display: none;
            }
        }
    }
}

.js {
    .employability-finder {
        .query-box {
            @include sr-only();
        }
    }
}

.employability-finder {
    .filter-box__filter__content {
        select {
            background-color: $color-concrete;
            border: $border-radius-thin solid $color-black;
            bottom: $measure-xsmall;
            color: $color-black;
            height: $measure-xxlarge;
            position: relative;
            width: 100%;
            font-weight: $font-weight-bold;
        }

        .sr-only {
            position: initial;
            width: initial;
            height: initial;
            padding: $measure-medium 0;
            margin: initial;
            overflow: initial;
            clip: initial;
            border: 0;
        }
    }
}

.employability-finder__results__item {
    display: block;
    @include box-shadow(delta, rgba(0, 0, 0, 0.3));
    @include anchor-reset();
    margin-bottom: $measure-medium;
}

.employability-finder__results__item__heading {
    padding: $measure-medium;
    background-color: $color-red;

    h3 {
        color: $color-white;
        font-size: $font-size-gamma;
        font-weight: $font-weight-medium;
        margin-bottom: initial;
    }
}

.employability-finder__results__item__description {
    padding: $measure-medium;
}

.employability-finder__results__item__info__code,
.employability-finder__results__item__info__type {
    padding: $measure-medium $measure-medium 0 $measure-medium;
    font-weight: bold;
    margin-bottom: initial;
}

.filter-box__reset {
    padding: $measure-small $measure-medium;
    background-color: $color-red;
    color: $color-white;
    font-weight: $font-weight-medium;
    font-size: $font-size-delta;
}

.employability__hr {
    border-bottom: 0 $border-style-default rgba($color-black, 1);
    width: 100%;
    margin-top: $measure-large;
}
