////
/// Reset
/// @group base
////

html {
    -ms-text-size-adjust: 100%;
    -webkit-text-size-adjust: 100%;
}

body {
    margin: 0;
    text-rendering: optimizeLegibility;
    -moz-osx-font-smoothing: auto;
    -webkit-font-smoothing: auto;
}

h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
figure,
ol,
ul {
    margin: 0;
    padding: 0;
}

main {
    display: block;
}

h1,
h2,
h3,
h4,
h5,
h6 {
    font-size: inherit;
}

strong {
    font-weight: 700;
}

a,
button {
    color: inherit;
    transition: all $transition-duration $transition-function;
}

a {
    text-decoration: none;
}

button {
    background: none;
    border: 0;
    overflow: visible;
    font: inherit;
    letter-spacing: inherit;
    -webkit-font-smoothing: inherit;
    cursor: pointer;
}

::-moz-focus-inner {
    padding: 0;
    border: 0;
}

img {
    max-width: 100%;
    height: auto;
    border: 0;
}
