.event__img {
    margin: 1rem auto;
}

.event__calendar {
    @include grid;
    font-family: $font-family-lato;
    font-size: $font-size-delta;
    font-weight: $font-weight-heavy;
    margin-bottom: $measure-medium;
    margin-top: $measure-small;
    margin-left: $measure-medium;

    .event__calendar__end,
    .event__calendar__start {
        background-color: $color-hub;
        padding: $measure-medium;
        text-align: center;
        min-width: grid-column-width(1);
        max-width: grid-column-width(2);
        text-align: center;
    }

    .event__calendar__separator {
        text-align: center;
        width: grid-column-width(1);
        align-self: center;
    }

    .event__calendar__day {
        display: block;
        font-size: $font-size-beta;

        @include media('<=small') {
            font-size: $font-size-delta;
        }
    }

    .event__calendar__month {
        display: block;
        font-size: $font-size-small;
        text-transform: uppercase;
    }
}

.event__times {
    margin-top: $measure-medium;
    margin-bottom: $measure-medium;
}

.student-events-finder {
    margin-top: -#{$measure-medium * 2};
    overflow-x: hidden;

    .student-events-finder__wrapper {
        @include grid;
        min-height: 15.5rem;
    }
}

.student-events-finder {
    .student-events-finder__search-form {
        @include grid-column(6);
        @include grid-column-push(3);

        @include media('<=large') {
            @include grid-column(10);
            @include grid-column-push(1);
        }

        @include media('<=medium') {
            @include grid-column(12);
        }

        align-self: center;
    }

    .student-events-finder__top {
        @include grid;
        min-height: 10.5rem;

        @include media('<=medium') {
            margin-left: $measure-small;
            margin-right: $measure-small;
        }
    }

    .student-events-finder__search-form {
        display: block;
        margin: 3.5rem $measure-small 0;
        position: relative;
        text-align: left;
    }

    .query-box__label {
        color: $color-white;
        display: block;
        font-family: $font-family-lato;
        font-size: $font-size-default;
        font-weight: $font-weight-semibold;
        margin-left: $measure-small;
    }

    .query-box__query {
        font-family: $font-family-default;
        font-size: $font-size-default;
        font-weight: $font-weight-default;
        height: $measure-xlarge;
        margin: 0 0 0 $measure-small;
        padding: $measure-small;
        vertical-align: top;
        width: grid-column-width(10);

        @include media('<=min') {
            width: grid-column-width(9);
        }

        &:focus {
            outline: $border-width-default $border-style-default $color-hub;
        }
    }

    .query-box__button {
        @extend .button--icon;
        width: $measure-xlarge;
        height: $measure-xlarge;
        vertical-align: top;

        span:before {
            @include font-family(icon);
            content: $fa-var-search;
            font-size: $font-size-gamma;
            font-weight: $font-weight-bold;
            line-height: $font-size-gamma;
        }
    }

    .student-events-finder__main {
        @include grid;
        background-color: $color-gallery;
        padding-bottom: $measure-large;

        @include media('<=medium') {
            margin-left: $measure-small;
            margin-right: $measure-small;
        }
    }

    .student-events-finder__main__left {
        @include grid-column(2);
        @include grid-column-push(1);
    }

    .student-events-finder__main__right {
        @include grid-column(7);
        @include grid-column-push(2);
    }

    .student-events-finder__results__count {
        margin-bottom: $measure-xlarge;
        padding-left: $measure-medium;
        text-align: center;
        width: 100%;

        span span {
            font-weight: $font-weight-semibold;
        }
    }

    .student-events-finder__results__refresh {
        display: inline-block;
        color: $color-hub;
        margin-left: $measure-small;
    }

    .fade-out {
        opacity: 0;
        pointer-events: none;
        user-select: none;
        animation: fade-out 1s linear;
    }

    .student-events-finder__results {
        @include grid;
        padding-left: $measure-medium + $measure-small;
        padding-right: $measure-medium + $measure-small;
    }

    .student-events-finder__results__list {
        @include grid;
        padding-left: 1rem;
    }

    .student-events-finder__card {
        @include box-shadow(delta, rgba(0, 0, 0, 0.15));

        margin-bottom: $measure-medium;
        margin-right: $measure-medium;
        width: 100%;

        @include media('>between') {
            width: calc(#{33%} - #{$measure-medium});
        }

        a {
            @include anchor(search-result);
        }
    }

    .student-events-finder__card {
        .student-events-finder__card__link {
            height: 100%;

            .student-events-finder__card__title {
                font-size: $font-size-delta;
                margin-bottom: $measure-small;
                text-align: center;

                @include media('<=between') {
                    font-size: $font-size-default;
                    text-align: left;
                }
            }

            .student-events-finder__card__description,
            .student-events-finder__card__title {
                width: 100%;
            }
        }

        time {
            display: block;
            margin-top: $measure-medium;
            text-align: center;
        }
    }
}
