////
/// Accordion Tabs
/// @group components
////

.tabs {
    background: $color-white;
}

.tabs__content__group {
    overflow: hidden;
    padding: $measure-small;
    width: 100%;
}

.home--news-and-events {
    margin-top: $measure-medium;
    .tas__content__group {
        padding: 0;
    }
}

.tabs__content__group__shame {
    .tabs__content__heading-anchor.active + & {
        @include animation(slide-top-in);
    }
}

////
// tabs/accordion
////

.js {
    .tabs__content__heading-anchor {
        &.inactive + div {
            @include visually-hidden;
            display: none;
        }
    }

    .tabs:not(.tabs--nac) {
        .tabs__content__heading,
        .tabs__menu-fullwidth,
        .tabs__menu {
            @include media('<=small') {
                @include visually-hidden;
            }
        }

        .tabs__content__heading-anchor {
            @include media('>small') {
                @include visually-hidden;
                &:not([aria-selected='true']) {
                    display: none;
                }
            }
        }
    }

    .tabs.tabs--nac {
        .tabs__content__heading-anchor {
            @include visually-hidden;
            &:not([aria-selected='true']) {
                display: none;
            }
        }
    }

    .tabs__content__group > div {
        @include media('>small') {
            padding-top: $measure-medium;
            padding-bottom: $measure-large;
        }

        @include media('<=small') {
            margin-top: $measure-medium;
            margin-bottom: $measure-medium;
        }
    }

    .tabs__content__heading-anchor__icon {
        @extend .fa;
        @extend .fa-plus;
        float: right;
    }

    [aria-selected='true'] .tabs__content__heading-anchor__icon {
        @extend .fa-minus;
    }
}

////
// style
////

.js {
    .tabs {
        margin-bottom: $measure-large;
    }

    .tabs__content__heading-anchor {
        &.inactive + div {
            @include visually-hidden;
        }
    }

    .tabs__content__group > div {
        padding-top: $measure-medium;
    }

    .tabs__content__heading-anchor__icon {
        @extend .fa;
        @extend .fa-plus;
        float: right;
    }

    [aria-selected='true'] .tabs__content__heading-anchor__icon {
        @extend .fa-minus;
    }
}

.tabs__content__heading {
    @include visually-hidden;
}

.tabs__menu {
    display: flex;
    flex-direction: row;
}

.tabs__menu__item {
    @include anchor(activable-inline-normal);
    font-size: $font-size-default;

    flex: 1 1 auto;

    display: flex;
    align-items: center;
    justify-content: center;
}

.tabs__content__heading-anchor {
    @include anchor(activable-stacked-normal);
}

.tabs.tabs--allports {
    .tabs__content__heading-anchor {
        @include anchor(activable-stacked-normal--allports);
    }

    .tabs__menu__item {
        @include anchor(activable-inline-normal--allports);
    }
}

.tabs.tabs--reduceable {
    .tabs__menu .tabs__menu__item:only-of-type {
        pointer-events: none;
    }

    .tabs__content .tabs__content__heading-anchor:only-of-type {
        pointer-events: none;

        .tabs__content__heading-anchor__icon {
            display: none;
        }
    }
}
