// Put your crappy CSS here, and we can revisit it later so we can integrate it
// in our codebase properly! :)

.anchor-demo-list {
    @extend .default-list;

    li {
        padding: $measure-medium;

        @for $i from 0 through 10 {
            &:nth-child(#{$i + 1}) {
                background-color: mix($color-black, $color-white, $i * 10%);
                color: contrasting-color(
                    mix($color-black, $color-white, $i * 10%)
                );
            }
        }
    }
}

//used in UG, Career Pathways logos
.multiple-item-strip {
    & > div {
        display: inline-block;
        max-width: 120px;
    }
}

//Used in UG, the image when there are no stats
.section--vertical-split {
    background-image: linear-gradient(
        to top,
        $color-apricot 50%,
        $color-white 50%
    );

    img {
        width: 100%;
        height: auto;
    }

    @include media('<=small') {
        display: none;
    }
}

// not sure where to put this...
.inline-accreditation {
    margin-bottom: $measure-large;

    a {
        @include anchor(simple-image);
        display: inline-block;
        margin-right: $measure-medium;
        margin-bottom: $measure-small;
    }

    img {
        max-height: $measure-large;
    }
}

.wFormContainer {
    width: 100% !important;

    .supportInfo {
        display: none;
    }
}

@media all and (-ms-high-contrast: none), (-ms-high-contrast: active) {
    .indicator {
        display: none;
    }
}

#CybotCookiebotDialog .CybotCookiebotDialogBodyLink,
#CybotCookiebotDialogDetail a {
    border-bottom: none;
    transition: none;

    &:hover {
        background-color: unset;
    }
}
