////
/// Page
/// @group base
////

html {
    background-color: green !important; //test pipeline
    box-sizing: border-box;
    background-color: $color-lightgray;
    min-height: 100%;
    overflow-x: hidden;
    // prevent horizontal when jumping short page <--> tall page
    overflow-y: scroll;
    // set the box model for the entire document
    position: relative;
}

// Box model set to `inherit` by default so children of `content-box` elements
// inherit from the parent properly
*,
*:before,
*:after {
    box-sizing: inherit;
}

body {
    background-color: $color-white;
    color: $color-bodytext;
    @include media('<=medium') {
        overflow-x: hidden;
    }
}
