////
/// Quick alerts
/// @group components
////
.quick-alerts {
    @include grid;
    background-color: $color-alert-amber;
    color: $color-black;
    box-shadow: inset 0 12px 48px -12px rgba(0, 0, 0, 0.85),
        inset 0 -12px 48px -12px rgba(0, 0, 0, 0.85);
    max-height: 0;
    overflow-y: hidden;
    transition-duration: 1.5s;
    transition-property: all;
    transition-timing-function: cubic-bezier(0.785, 0.135, 0.15, 0.86);

    .quick-alert__more-info a {
        @include anchor('black');
    }

    .quick-alert__close a {
        @include anchor('cta--pita');
    }
}

.quick-alerts__wrapper {
    @include grid-top;
}

.quick-alerts.open {
    box-shadow: inset 0 12px 12px -12px rgba(0, 0, 0, 0.65),
        inset 0 -12px 12px -12px rgba(0, 0, 0, 0.65);
    max-height: 100vh;
}

.quick-alerts--red {
    background-color: $color-alert-red;
    color: $color-white;

    .quick-alert__more-info a {
        @include anchor('white');
    }

    .quick-alert__close a {
        @include anchor('cta--gtfo');
    }
}

.quick-alert {
    @include grid;
    @include grid-column(9);
    @include grid-column-push(1);
    padding: $measure-medium;
    align-self: center;
}

.quick-alert__sign {
    @include grid-column(1);
    font-size: $font-size-omicron;
    text-align: center;

    @include media('<=medium') {
        font-size: $font-size-gamma;
    }

    align-self: center;
}

.quick-alert__details {
    @include grid-column(10);
    @include grid-column-push(1);
}

.quick-alert__title {
    font-family: $font-family-lato;
    font-weight: $font-weight-heavy;
    font-size: $font-size-delta;
}

.quick-alert__description {
    font-family: $font-family-default;
    font-weight: $font-weight-default;
    font-size: $font-size-default;
}

.quick-alert__more-info {
    font-family: $font-family-default;
    font-weight: $font-weight-bold;
    font-size: $font-size-default;

    @include media('<=medium') {
        font-size: $font-size-small;
    }

    span {
        font-weight: $font-weight-default;
    }
}

.quick-alert__last-updated {
    font-family: $font-family-default;
    font-weight: $font-weight-bold;
    font-size: $font-size-default;

    @include media('<=medium') {
        font-size: $font-size-small;
    }

    span {
        font-weight: $font-weight-default;
    }
}

.quick-alert__close {
    @include grid-column(1);
    @include grid-column-push(1);
    margin-top: $measure-medium;
    margin-bottom: $measure-medium;
    align-self: center;
}
