////
/// Breadcrumbs
/// @group components
////

$chevron-padding-default: ($measure-medium * 3/4);
$chevron-padding-mobile: $measure-small;

.breadcrumbs {
    @include grid;
    @include grid-top;
    font-size: $font-size-small;
    padding-bottom: $measure-medium;

    @include media('>medium') {
        display: none;
    }
}

.breadcrumbs--always {
    @include media('>medium') {
        display: block;
    }
}

.breadcrumbs__list {
    @include grid-column(12);

    li {
        display: inline-block;
        padding-top: $border-width-hairline;
        padding-bottom: $border-width-hairline;
        padding-right: $chevron-padding-default;
    }

    li + li {
        position: relative;

        &:before {
            @include font-family(icon);
            content: '\f105';
            color: $color-lucifer;
            font-size: $font-size-default;
            padding-right: $chevron-padding-default;
        }
    }

    @include media('<=small') {
        li {
            padding-right: $chevron-padding-mobile;
        }

        li + li {
            &:before {
                padding-right: $chevron-padding-mobile;
            }
        }
    }
}
