////
/// Header
/// @group layout
////

.header {
    background-color: $color-black;
    color: $color-white;
    position: relative;
}
.header-v25 {
    display: flex;
    width: 100%;
    justify-content: center;
}

.header__wrapper {
    .header:not(.header-v25) & {
        //These can eventually be removed once 01/03/25 home pages styles have been applied
        @include grid-top;
    }
    .header-v25 & {
        display: grid;
        grid-template-columns: 1fr 1fr;
        gap: $measure-medium-relative;
        max-width: $grid-width-extra-large;
        width: 100%;
        padding: 0 $measure-medium-relative $measure-medium-relative
            $measure-medium-relative;
        @include media('>=medium') {
            grid-template-columns: 1fr minmax(350px, 1fr) 1fr;
        }
        @include media('>=large') {
            grid-template-columns: 1fr minmax(410px, 1fr) 1fr;
        }
    }
}
.header__right {
    display: flex;
    gap: $measure-medium;

    align-items: center;
    margin-top: $measure-medium + $measure-small;
    justify-content: flex-end;
}
.header__notice {
    background-color: $color-gallery;
    border-bottom-width: 0;
    color: inherit;
    display: block;
    flex: 1 0 100%;
    left: 50%;
    line-height: $line-height-small;
    padding: $measure-small;
    position: relative;
    text-align: center;
    top: -$grid-padding;
    transform: translateX(-50%);
    width: 100%;

    span {
        border-bottom: $border-width-hairline $border-style-default $transparent;

        &:after {
            content: ' ';
        }
    }

    &:hover,
    &:focus {
        span {
            border-bottom-color: currentColor;
        }
    }

    &:active {
        background-color: $color-white;
        color: contrasting-color($color-white);
    }
}

.header__logo {
    $header-logo-width: 110px;
    $header-logo-ratio: 0.683;

    .header-v25 & {
        display: flex;
    }
    .header:not(.header-v25) & {
        //These can eventually be removed once 01/03/25 home pages styles have been applied
        @include grid;
        padding-bottom: $measure-medium;
        width: grid-column-width(6);
        align-self: center;
        @include grid-column-map(
            (
                'min': 6,
                'small': 6,
                'between': 4,
                'medium': 5,
                'large': 4,
                'extralarge': 4,
            ),
            true,
            false
        );
    }

    //   width: 100% !important;

    a,
    img {
        display: block;
    }

    img {
        background-color: $color-white;
        margin-left: $measure-small;
        margin-right: $measure-small;
        padding: 36px 24px;
        width: $header-logo-width;
        min-width: $header-logo-width;
        height: $header-logo-width * (1 / $header-logo-ratio);

        @include media('<=medium') {
            display: none;
        }
    }

    .header__logo__parent {
        border: 0;
        background: none;

        &:hover {
            border: 0;
            background: none;
        }

        @include anchor(reset);
        @include anchor(skate--dark-bg);
    }

    .header__logo__home {
        @include anchor('reset');
        @include anchor('block--dark-bg');

        font-family: $font-family-lato;
        font-size: $font-size-alpha;
        font-weight: $font-weight-heavy;
        margin-left: $measure-medium;
        padding-top: $measure-medium + $measure-small;
        align-self: center;
        .header-v25 & {
            white-space: nowrap;
        }
        span {
            color: $color-hubgrey;
            font-weight: $font-weight-default;

            &:hover,
            &:focus,
            &:active {
                color: darken($color-hubgrey, 6%);
            }
        }

        @include media('>medium') {
            min-height: floor($header-logo-width * $header-logo-ratio);
        }

        @include media('<=medium') {
            font-size: $font-size-gamma;
        }

        @include media('<=min') {
            font-size: $font-size-delta;
        }
    }
}

.header__toggles {
    text-align: right;
}

.breadcrumbs-dropdown {
    display: none;
}
