////
/// Grid
/// @group layout
////

// The way in which Grids are formed/a combination of specific elements and
// classes in HTML is similar to the following:
//     <div class="container">
//         <div class="grid">
//             <div class="grid__half">...</div>
//             <div class="grid__half">...</div>
//         </div>
//         <div class="grid">
//             <div class="grid__4">...</div>
//             <div class="grid__4">...</div>
//             <div class="grid__4">...</div>
//         </div>
//         <div class="grid--quarters">
//             <div>...</div>
//             <div>...</div>
//             <div>...</div>
//             <div>...</div>
//         </div>
//     </div>

/**
 * Top-Level Grid
 */
.container {
    @include grid-top;

    & + & {
        margin-top: $measure-large;
    }
}
.container--full {
    max-width: 100%;
    padding-left: $grid-padding-half;
    padding-right: $grid-padding-half;
}

/**
 * Grid Container
 */
.grid,
%grid {
    @include grid;
}

/**
 * Grid Columns
 */
[class*='grid__'],
%grid-column {
    @include grid-column-base;
}
@for $i from 1 through $grid-columns {
    .grid__#{$i},
    %grid__#{$i} {
        @include grid-column($i, 'medium', false);
    }
}

/**
 * Push
 */
[class*='push__'] {
    @include media('<=medium') {
        left: 0;
    }
}
@for $i from 1 through ($grid-columns - 1) {
    .push__#{$i} {
        @include media('>medium') {
            left: grid-column-width($i);
        }
    }
}

/**
 * Pull
 */
[class*='pull__'] {
    @include media('<=medium') {
        right: 0;
    }
}
@for $i from 1 through ($grid-columns - 1) {
    .pull__#{$i} {
        @include media('>medium') {
            right: grid-column-width($i);
        }
    }
}

/**
 * Flex Ordering
 */
@for $i from 1 through ($grid-columns - 1) {
    .order__#{$i} {
        order: $i;
    }
}

/**
 * Floats
 */
@each $type in (left, right, none) {
    .float-#{$type},
    %float-#{$type} {
        float: #{$type} !important;
    }
    @each $name, $value in $breakpoints {
        .float-#{$type}--below-#{$name} {
            @include media('<=#{$name}') {
                float: #{$type} !important;
            }
        }
        .float-#{$type}--above-#{$name} {
            @include media('>#{$name}') {
                float: #{$type} !important;
            }
        }
    }

    @each $breakpoint-first, $breakpoint-second in unique-groups($breakpoints) {
        @each $breakpoint-first-key,
            $breakpoint-first-value in $breakpoint-first
        {
            @each $breakpoint-second-key,
                $breakpoint-second-value in $breakpoint-second
            {
                .float-#{$type}--#{$breakpoint-first-key}-#{$breakpoint-second-key} {
                    @include media(
                        '>#{$breakpoint-first-key}',
                        '<=#{$breakpoint-second-key}'
                    ) {
                        float: #{$type} !important;
                    }
                }
            }
        }
    }
}

//
// Aliases
//

.grid__sixth,
%grid__sixth {
    @extend %grid__2;
}

.grid__quarter,
%grid__quarter,
.grid__fourth,
%grid__fourth {
    @extend %grid__3;
}

.grid__third,
%grid__third {
    @extend %grid__4;
}

.grid__half,
%grid__half {
    @extend %grid__6;
}

.grid__full,
%grid__full,
.grid__whole,
%grid__whole {
    @extend %grid__12;
}

.grid--sixths,
%grid--sixths {
    @extend %grid;

    & > div,
    & > li {
        @include grid-column-base;

        @include media('<=small') {
            width: 100%;
        }

        @include media('>small', '<=medium') {
            width: grid-column-width(4);
        }

        @include media('>medium') {
            width: grid-column-width(2);
        }
    }
}

.grid--quarters,
%grid--quarters,
.grid--fourths,
%grid--fourths {
    @extend %grid;

    & > div,
    & > li {
        @include grid-column-base;

        @include media('<=small') {
            width: 100%;
        }

        @include media('>small', '<=medium') {
            width: grid-column-width(6);
        }

        @include media('>medium') {
            width: grid-column-width(3);
        }
    }
}

.grid--quarters--alternate,
%grid--quarters--alternate,
.grid--fourths--alternate,
%grid--fourths--alternate {
    @extend %grid;

    & > div,
    & > li {
        @include grid-column-base;

        @include media('<=small') {
            width: 100%;
        }

        @include media('>small', '<=medium') {
            width: grid-column-width(6);
        }

        @include media('>medium', '<=large') {
            width: grid-column-width(4);
        }

        @include media('>large') {
            width: grid-column-width(3);
        }
    }
}

.grid--thirds,
%grid--thirds {
    @extend %grid;

    & > div,
    & > li {
        @include grid-column(4);
    }
}

.grid--thirds--alternate,
%grid--thirds--alternate,
.grid--thirds--small,
%grid--thirds--small {
    @extend %grid;

    & > div,
    & > li {
        @include grid-column(4, 'small');
    }
}

.grid--halves,
%grid--halves {
    @extend %grid;

    & > div,
    & > li {
        @include grid-column(6);
    }
}

.grid--halves--alternate,
%grid--halves--alternate,
.grid--halves--small,
%grid--halves--small {
    @extend %grid;

    & > div,
    & > li {
        @include grid-column(6, 'small');
    }
}
