////
/// Dialog
/// @group components
////

.dialog {
    background: rgba($color-black, 0.7);
    width: 100%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
    position: fixed;
    top: 0;
    left: 0;
    z-index: z-index(modal);
}

.dialog__box {
    width: auto;
    max-width: calc(100% - 1rem);
    height: auto;
    max-height: calc(100% - 1rem);
    display: inline-block;
    flex: auto;
    overflow-y: auto;

    @include media('>medium') {
        max-width: 50%;
        max-height: 70%;
    }
}

.dialog__box__heading {
    @include font-family(lato);
    background-color: $color-white;
    font-size: $font-size-beta;
    position: relative;

    @include media('>small') {
        padding: $measure-xlarge $measure-xlarge $measure-large;
    }

    @include media('<=small') {
        padding: $measure-xlarge $measure-medium $measure-large;
    }
}

.dialog__box__content {
    background-color: $color-white;

    @include media('>small') {
        padding: 0 $measure-xlarge $measure-xlarge;
    }

    @include media('<=small') {
        padding: 0 $measure-medium $measure-xlarge;
    }
}

.dialog__box__heading__close {
    position: absolute;
    top: $measure-large;
    right: $measure-medium;
    @include anchor(skate);
}

.dialog__box__heading__close__icon {
    @extend .fa;
    @extend .fa-close;
}
