////
/// Twitter box
/// @group components
////

.twitter-box {
    border: $border-style-default $border-width-default $color-gallery;
    border-radius: $border-radius-default;

    @include media('<=medium') {
        margin-top: $measure-large;
        margin-bottom: $measure-large;
    }
}

.twitter-box--right {
    @include media('>medium') {
        margin-left: $measure-small;
    }

    @include media('>large') {
        margin-left: $measure-medium;
    }
}
