a.vertical-menu__page-link,
a.vertical-menu__toggle-link {
    @include anchor(reset);
    @include anchor(nav-mobile-link);
    z-index: z-index(default);
}
span.vertical-menu__page-link {
    display: block;
}

.vertical-menu__page-link {
    margin-right: #{$measure-medium * 3};
}

.vertical-menu--l1 > .vertical-menu__header > .vertical-menu__page-link {
    padding: $measure-medium;
    position: relative;
    z-index: z-index(default);
}

.vertical-menu__page-link.vertical-menu__page-link--t2 {
    pointer-events: none;

    span {
        border-bottom: $border-width-default $border-style-default $color-hub;
    }
}

.vertical-menu__toggle-link {
    @include absolute(top-right);
    font-size: $font-size-small;
    height: 100%;

    span {
        @extend .fa-chevron-down;
    }

    .vertical-menu.open > .vertical-menu__header & span {
        @extend .fa-chevron-up;
    }
}

.vertical-menu__icon {
    margin-right: 1rem;
}

.vertical-menu--l0 > .vertical-menu__header {
    border-bottom: $border-width-thick $border-style-default $color-emperor;
}

$level-colors: $color-black, $color-black, $color-black,
    lighten($color-black, 10%), lighten($color-black, 20%);
@for $i from 1 through length($level-colors) {
    .vertical-menu--l#{$i - 1} {
        $color: nth($level-colors, $i);
        background-color: $color;
        color: contrasting-color($color);

        @if ($i > 2) {
            & > .vertical-menu__header > .vertical-menu__page-link {
                padding-left: $measure-large;
            }
        }
    }
}

.vertical-menu--l0 {
    & > .vertical-menu__header {
        color: $color-white;
        font-family: $font-family-lato;
        font-size: $font-size-gamma;
        font-weight: $font-weight-heavy;
    }
}
.vertical-menu--l1 {
    & > .vertical-menu__header {
        font-weight: $font-weight-semibold;
    }
}

.vertical-menu--l1.open {
    & > .vertical-menu__header,
    & > ul {
        border-bottom: $border-width-hairline $border-style-default
            $color-emperor;
    }
}

.vertical-menu__header {
    position: relative;
}

.vertical-menu {
    ul:not([class]) {
        margin: 0 !important;
    }

    li {
        list-style: none;
        position: relative;
    }

    &.vertical-menu--left {
        right: 100%;
        left: -100%;
    }
    &.vertical-menu--center {
        right: 0;
        left: 0;
    }
    &.vertical-menu--right {
        right: -100%;
        left: 100%;
    }

    &:not([class*='open']) > ul {
        display: none;
    }
}

.nav-mobile {
    .vertical-menu--l1 > .vertical-menu__header span {
        padding-top: 0.75rem;
    }
}

.vertical-menu__header a {
    display: flex;
    .fa {
        font-size: $font-size-gamma;
    }
}

.new-nav {
    .vertical-menu--l1:nth-child(3),
    .vertical-menu--l1:nth-child(4) {
        border-bottom: $border-width-thick $border-style-default $color-emperor;
    }
}
