////
/// Content
/// @group base
////

html {
    @include font-family(default);
    font-size: $base-font-size;
    font-style: normal;
    font-weight: $font-weight-default;
    line-height: $line-height-default;
    font-feature-settings: 'lnum' 1;
}

/**
 * Content Elements
 */
p,
ul,
ol,
dl,
figure,
table,
blockquote {
    @extend %content-elements !optional;
}

/**
 * Heading Elements
 */
h1,
h2,
h3,
h4,
h5,
h6 {
    @extend %heading-elements !optional;
    @include font-family(lato);
    width: 100%;
    display: block;
    flex: 1 0 auto;
    position: relative;
    margin-bottom: $measure-medium;
    font-weight: $font-weight-default;
}

.alpha,
%alpha {
    @include typographic-scale($font-size-alpha);
}
h1 {
    @extend %alpha !optional;
    font-weight: $font-weight-heavy;
}

.beta,
%beta {
    @include typographic-scale($font-size-beta);
}
h2 {
    @extend %beta !optional;
    font-weight: $font-weight-heavy;
}

.gamma,
%gamma {
    @include typographic-scale($font-size-gamma);
}
h3 {
    @extend %gamma !optional;
    font-weight: $font-weight-heavy;
}

.delta,
%delta {
    @include typographic-scale($font-size-delta);
}
h4 {
    @extend %delta !optional;
    font-weight: $font-weight-heavy;
}

.epsilon,
%epsilon {
    @include typographic-scale($font-size-default);
}
h5 {
    @extend %epsilon !optional;
    font-weight: $font-weight-black;
}

.zeta,
%zeta {
    @include typographic-scale($font-size-default);
}
h6 {
    @extend %zeta !optional;
    font-weight: $font-weight-black;
}

p {
    margin: 0 0 #{$line-height-default}em;

    &:empty {
        display: none;
        visibility: hidden;
    }

    &.columns {
        @include media('>large') {
            text-align: justify;
            column-count: 2;
            column-gap: $measure-large-relative;
        }
    }
}

/**
 * Lists
 */
ol,
ul {
    padding: 0;
    margin: 0;
    list-style: none;
}
ol:not([class]),
ul:not([class]),
.default-list {
    list-style-position: outside;
    margin-bottom: $measure-medium;
    margin-left: $measure-medium;

    // List nested in a list
    & & {
        margin-top: $measure-medium;
    }
}
ul:not([class]),
ul.default-list {
    list-style-type: disc;
}
ol:not([class]),
ol.default-list {
    list-style-type: decimal;

    &[reversed] {
        display: flex;
        flex-direction: column-reverse;
    }
}

figure {
    max-width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin: 0 0 $measure-medium;

    figcaption {
        @include font-family(serif);
        display: flex;
        flex-direction: column;
        align-items: center;
        padding-top: $measure-medium;
        font-style: italic;
        font-size: $font-size-small;
        line-height: $line-height-small;
    }

    & > a {
        display: block;
    }

    img {
        width: 100%;
    }
}

/**
 * Quotes
 */
blockquote {
    @include quotes;
    border-left: $border-width-fat $border-style-default $color-hub;
    font-size: $font-size-delta;
    margin-bottom: #{$line-height-default}em;
    padding-left: $measure-large;
    padding-right: $measure-large;
    padding-top: $measure-small;

    p {
        position: relative;

        &:before,
        &:after {
            font-size: $font-size-omicron;
            position: absolute;
            text-align: center;
            width: #{$line-height-default}rem;
        }

        &:before {
            content: open-quote;
            left: -#{$line-height-default}rem;
            top: -$measure-medium;
        }

        &:after {
            bottom: -#{$measure-large + $measure-small};
            content: close-quote;
            right: -#{$line-height-default}rem;
        }

        &:not(:last-of-type):after,
        &:not(:first-of-type):before {
            display: none;
        }

        &:last-of-type {
            margin-bottom: 0;
        }
    }

    cite {
        display: block;
        font-style: italic;
        padding: $measure-medium;
        text-align: right;

        &:before {
            content: '  ';
        }
    }
}

p.disclaimer {
    border-left: $border-width-fat $border-style-default $color-hub;
    padding-left: $measure-medium;
}

small {
    font-size: $font-size-small;
}

sub,
sup {
    position: relative;
    vertical-align: baseline;
    line-height: 0;
}

sup {
    top: -0.5em;
}

sub {
    bottom: -0.25em;
}
/**
 * Neuter bad editors
 **/
a,
h1,
h2,
h3,
h4,
h5,
h6 {
    b,
    strong {
        font-weight: inherit;
    }
}

iframe[src*='prezi'] {
    display: none !important;
}
