////
/// Mixins
/// @group abstracts
////

///
/// Add a border-radius.
///
/// @author Chris Burnell
///
/// @param {String} $thickness - Thickness of Border Radius
/// @param {String} $border-radius - Type of Border Radius
///
/// @require {Variable} $border-radius-default
/// @require {Variable} $border-radius-thin
/// @require {Variable} $border-radius-hairline
/// @require {Map} $border-radii
///
/// @example scss - Zero parameters
///   @include radius;
///   // border-radius: 0.250rem;
///
/// @example scss - Pre-defined thickness
///   @include radius(thin);
///   // border-radius: 0.125rem;
///
/// @example scss - Custom thickness
///   @include radius(1em);
///   // border-radius: 1em;
///
/// @example scss - One corner
///   @include radius(top-left);
///   // border-radius: 0.250 0 0 0;
///
/// @example scss - One side
///   @include radius(bottom);
///   // border-radius: 0 0 0.250rem 0.250rem;
///
/// @example scss - Custom thickness
///   @include radius(thin, bottom);
///   // border-radius: 0 0 0.125rem 0.125rem;
///
@mixin border-radius($thickness: default, $border-radius: null) {
    @if not is-length($thickness) {
        @if map-has-key($border-radii, $thickness) {
            $thickness: map-get($border-radii, $thickness);
        } @else if is-edge($thickness) {
            $border-radius: $thickness;
            $thickness: $border-radius-default;
        } @else {
            @error "There is no `thickness` named `#{$thickness}` in `$border-radii`. `thickness` should be one of `#{map-keys($border-radii)}`.";
        }
    }

    @if is-edge($border-radius) {
        $top-left: if(
            $border-radius ==
                'top' or
                $border-radius ==
                'left' or
                $border-radius ==
                'top-left',
            $thickness,
            0
        );
        $top-right: if(
            $border-radius ==
                'top' or
                $border-radius ==
                'right' or
                $border-radius ==
                'top-right',
            $thickness,
            0
        );
        $bottom-right: if(
            $border-radius ==
                'bottom' or
                $border-radius ==
                'right' or
                $border-radius ==
                'bottom-right',
            $thickness,
            0
        );
        $bottom-left: if(
            $border-radius ==
                'bottom' or
                $border-radius ==
                'left' or
                $border-radius ==
                'bottom-left',
            $thickness,
            0
        );

        $border-radius: $top-left $top-right $bottom-right $bottom-left;
    } @else {
        $border-radius: $thickness;
    }

    background-clip: padding-box;
    border-radius: $border-radius;
}

///
/// Clear an element of floats.
///
/// @example scss
///   @include clear;
///   // clear: both;
///   // zoom: 1;
///   // &:before,
///   // &:after {
///   //     content: "";
///   //     display: table;
///   // }
///   // &:after {
///   //     clear: both;
///   // }
///
@mixin clear() {
    clear: both;
    zoom: 1;

    &:before,
    &:after {
        content: '';
        display: block;
    }

    &:after {
        clear: both;
    }
}

///
/// Specify font-family
///
/// @author Chris Burnell
///
/// @param {String} $family - Font-family value
///
/// @require {Map} $font-families
///
/// @example scss - One family
///   @include font-family(default);
///   // font-family: "Proxima Nova", sans-serif;
///
/// @example scss - One family, one display
///   @include font-family(serif, auto);
///   // font-family: Georgia, serif;
///
/// @throw Error if no parameters are passed.
/// @throw Error if parameter passed does not match a key in $font-families.
///
@mixin font-family($family) {
    @if not $family {
        @error "font-family() expects at least one parameter.";
    }

    @if not
        map-has-key($font-families, $family) and not
        map-has-key($font-families-styles, $family)
    {
        @error "There is no `font-family` named `#{$family}` in `$font-families`. `font-family` should be one of `#{map-keys($font-families)}` or `#{map-keys($font-families-styles)}`.";
    }

    font-family: map-get($font-families, $family);
}

///
/// Hyphenate text.
///
@mixin hyphenate() {
    overflow-wrap: break-word;
    hyphens: auto;
}

///
/// Make text non-selectable.
///
@mixin non-selectable() {
    user-select: none;
    -webkit-touch-callout: default;
    -webkit-touch-callout: none;
}

///
/// Make the text inside an element hidden.
///
@mixin hide-text() {
    overflow: hidden;
    text-indent: 100%;
    white-space: nowrap;
}

///
/// Generate transitions based on flexible input parameters.
/// Pass zero or more properties and zero to two further parameters.
/// If passing two or more properties, quotation marks must be used.
///
/// @author Chris Burnell
///
/// @param {ArgList} $values - Values
///
/// @require {Variable} $transition-duration
/// @require {Variable} $transition-function
///
/// @example scss - Zero parameters
///   @include transition;
///   // transition: all 0.2s ease;
///
/// @example scss - One property
///   @include transition(color);
///   // transition: color 0.2s ease;
///
/// @example scss - Two properties
///   @include transition(color, border);
///   // transition: color 0.2s ease, border 0.2s ease;
///
/// @example scss - One property, one duration
///   @include transition(color 0.5s);
///   // transition: color 0.5s ease;
///
/// @example scss - One property, one duration, one timing function
///   @include transition(color 1s linear);
///   // transition: color 1s linear;
///
/// @example scss - Two properties, two durations, two timing functions
///   @include transition(color 2s ease-in-out, border 1s linear);
///   // transition: color 2s ease-in-out, border 1s linear;
///
@mixin transition($values...) {
    $transition: ();

    // if no parameter, transition all with default time and function
    @if length($values) == 0 {
        $transition: all #{$transition-duration} #{$transition-function};
    }
    // if parameter just set to "none" then just transition nothing (none)
    @else if $values == none {
        $transition: none;
    }
    // if the parameter contains a comma then operating on multiple properties
    @else if str-index(#{$values}, ',') {
        @each $value in $values {
            $transition: append($transition, unpack-transition($value), comma);
        }
    } @else {
        $transition: unpack-transition($values...);
    }

    transition: #{$transition};
}

///
/// Visually hide an element properly.
///
@mixin visually-hidden() {
    width: 1px !important;
    height: 1px !important;
    padding: 0 !important;
    border: 0 !important;
    overflow: hidden !important;
    position: absolute !important;
    clip: rect(1px, 1px, 1px, 1px) !important;
}

///
/// Modular Typographic Scale
///
/// @param {Number} $font-size - Font size in rem
///
/// @require {Number} $font-size-default
/// @require {Number} $line-height-default
///
/// @throw Error if no parameter is passed or the parameter's units are not in rem.
///
@mixin typographic-scale($font-size: $font-size-default) {
    @if not $font-size or unit($font-size) != 'rem' {
        @error "typographic-scale() expects a `font-size` parameter with `rem` units.";
    }

    $line-height: ceil($font-size / $line-height-default) * $line-height-default /
        $font-size;

    font-size: px($font-size);
    font-size: $font-size;
    line-height: decimal-ceil($line-height, 3);
}

///
/// Define characters for quotes
///
/// @author Chris Burnell
///
/// @example scss
///   @include quotes;
///   // quotes: "" "" "" "";
///
@mixin quotes() {
    hanging-punctuation: first;

    &,
    :lang(en) & {
        quotes: '' '' '' '';
    }

    :lang(de) &,
    :lang(pl) & {
        quotes: '' '' '' '';
    }

    :lang(fr) & {
        quotes: ' ' ' ' ' ' ' ';
    }
}

///
/// Absolutely position an element.
///
/// @author Chris Burnell
///
/// @param {String} $position - Position of the element
/// @param {String} $offset [0] - How much to offset from the position
///
/// @example scss
///   @include absolute(bottom-left);
///   // position: absolute;
///   // bottom: 0;
///   // left: 0;
///
/// @example scss
///   @include absolute(right, 1em);
///   // position: absolute;
///   // right: 1em;
///   // top: 50%;
///   // transform: translateY(-50%);
///
/// @example scss
///   @include absolute(top-full);
///   // position: absolute;
///   // top: 0;
///   // right: 0;
///   // left: 0;
///
/// @example scss
///   @include absolute(top-full, 1em);
///   // position: absolute;
///   // top: 1em;
///   // right: 0;
///   // left: 0;
///
/// @example scss
///   @include absolute(top-full-offset, 1em);
///   // position: absolute;
///   // top: 1em;
///   // right: 1em;
///   // left: 1em;
///
/// @throw Error if no parameters are passed.
///
@mixin absolute($position, $offset: 0) {
    @if not $position {
        @error "absolute() expects one parameter.";
    } @else if $position == none {
        position: relative;
        top: auto;
        right: auto;
        bottom: auto;
        left: auto;
    } @else if $position == center {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translateX(-50%) translateY(-50%);

        .lt-ie10 & {
            top: 0;
            left: 0;
            transform: none;
        }
    } @else if str-index(#{$position}, 'full') {
        position: absolute;

        @if str-index(#{$position}, 'top') {
            @if str-index(#{$position}, 'sides') {
                top: 0;
            } @else {
                top: $offset;
            }

            @if str-index(#{$position}, 'offset') {
                right: $offset;
                left: $offset;
            } @else {
                right: 0;
                left: 0;
            }
        } @else if str-index(#{$position}, 'right') {
            @if str-index(#{$position}, 'sides') {
                right: 0;
            } @else {
                right: $offset;
            }

            @if str-index(#{$position}, 'offset') {
                top: $offset;
                bottom: $offset;
            } @else {
                top: 0;
                bottom: 0;
            }
        } @else if str-index(#{$position}, 'bottom') {
            @if str-index(#{$position}, 'sides') {
                bottom: 0;
            } @else {
                bottom: $offset;
            }

            @if str-index(#{$position}, 'offset') {
                right: $offset;
                left: $offset;
            } @else {
                right: 0;
                left: 0;
            }
        } @else if str-index(#{$position}, 'left') {
            @if str-index(#{$position}, 'sides') {
                left: 0;
            } @else {
                left: $offset;
            }

            @if str-index(#{$position}, 'offset') {
                top: $offset;
                bottom: $offset;
            } @else {
                top: 0;
                bottom: 0;
            }
        } @else {
            top: $offset;
            right: $offset;
            bottom: $offset;
            left: $offset;
        }
    } @else if is-edge($position) {
        position: absolute;

        @if str-index(#{$position}, 'top') {
            top: $offset;
            @if not str-index(#{$position}, '-') {
                left: 50%;
                transform: translateX(-50%);

                .lt-ie10 & {
                    left: 0;
                    transform: none;
                }
            }
        }
        @if str-index(#{$position}, 'right') {
            right: $offset;
            @if not str-index(#{$position}, '-') {
                top: 50%;
                transform: translateY(-50%);

                .lt-ie10 & {
                    top: 0;
                    transform: none;
                }
            }
        }
        @if str-index(#{$position}, 'bottom') {
            bottom: $offset;
            @if not str-index(#{$position}, '-') {
                left: 50%;
                transform: translateX(-50%);

                .lt-ie10 & {
                    left: 0;
                    transform: none;
                }
            }
        }
        @if str-index(#{$position}, 'left') {
            left: $offset;
            @if not str-index(#{$position}, '-') {
                top: 50%;
                transform: translateY(-50%);

                .lt-ie10 & {
                    top: 0;
                    transform: none;
                }
            }
        }
    }
}

///
/// Simplify Box Shadows
///
@mixin box-shadow($box-shadow-type, $color: $color-black) {
    box-shadow: box-shadow($box-shadow-type) $color;
}

///
/// Reset Font Size
///
@mixin reset-font-size($child: null, $font-size: $font-size-default) {
    font-size: 0;

    @if ($child) {
        #{$child} {
            font-size: $font-size;
        }
    } @else {
        & > * {
            font-size: $font-size;
        }
    }
}
