.noticeboard-nest {
    @include grid;

    background-color: $color-gallery;
    padding-top: 2 * $measure-large;
    padding-bottom: $measure-large;
}

.noticeboard-nest__wrapper {
    @include grid-top;

    .cta-group {
        width: 100%;

        @include media('>small') {
            margin-right: $measure-small;
            position: absolute;
            right: 0;
            top: 0;
            width: grid-column-width(1);
        }
    }
}

.noticeboard-nest {
    .noticeboard__list.active {
        .noticeboard__notice {
            display: none;

            &.active {
                .noticeboard__notice__close {
                    display: block;
                }

                .noticeboard__notice__details {
                    display: block;
                }

                display: block;
                @include media('>medium') {
                    width: grid-column-width(8);
                    margin-left: grid-column-width(2);
                }
            }
        }
    }
}

.noticeboard-nest__link-more {
    font-family: $font-family-lato;
    font-weight: $font-weight-heavy;
    margin: $measure-medium 0 0;
    position: relative;
    text-align: right;
    width: 100%;

    @include media('<=extralarge') {
        padding-right: $measure-small;
    }

    a {
        @include anchor(skate);

        span {
            font-weight: $font-weight-default;
        }
    }
}
