////
/// Announcements
/// @group components
////

.no-js {
    .announcements {
        .announcements__list {
            .announcements__announcement {
                display: none;

                &:first-of-type {
                    display: block;
                }

                &:nth-of-type(2) {
                    @include media('>medium') {
                        display: block;
                    }
                }
            }
        }
    }
}

@mixin greyout() {
    .announcements__announcement__details {
        background-image: linear-gradient(
            rgba(12, 12, 14, 0.85),
            rgba(12, 12, 14, 0.85) 100%
        );
        transition-duration: 1s;
        transition-property: background-image;
        transition-timing-function: cubic-bezier(0.785, 0.135, 0.15, 0.86);
    }
    .announcements__announcement__title {
        color: $color-lucifer;
        transition-duration: 1s;
        transition-property: color;
        transition-timing-function: cubic-bezier(0.785, 0.135, 0.15, 0.86);

        .announcements__announcement__title__text {
            border-bottom-color: $color-lucifer !important;
            transition-duration: 1s;
            transition-property: border-bottom-color;
            transition-timing-function: cubic-bezier(0.785, 0.135, 0.15, 0.86);
        }
    }
    .announcements__announcement__description {
        color: $color-lucifer;
        transition-duration: 1s;
        transition-property: color;
        transition-timing-function: cubic-bezier(0.785, 0.135, 0.15, 0.86);
    }
    .announcements__announcement__published {
        color: $color-lucifer;
        transition-duration: 1s;
        transition-property: color;
        transition-timing-function: cubic-bezier(0.785, 0.135, 0.15, 0.86);
    }
}

.announcements {
    @include grid;

    &[data-page='1'] {
        .announcements__page-controls {
            li:nth-child(1) {
                button {
                    &:before {
                        content: '\F111';
                    }
                }
            }
        }

        @include media('>medium') {
            .announcements__announcement:nth-child(3) {
                @include greyout();
            }
        }

        @include media('<=medium') {
            .announcements__announcement:nth-child(2) {
                @include greyout();
            }
        }
    }

    &[data-page='2'] {
        .announcements__page-controls {
            li:nth-child(2) {
                button {
                    &:before {
                        content: '\F111';
                    }
                }
            }
        }

        &[data-announcements='4'],
        &[data-announcements='5'],
        &[data-announcements='6'] {
            @include media('>medium') {
                .announcements__announcement:nth-child(2),
                .announcements__announcement:nth-child(5) {
                    @include greyout();
                }
            }
        }

        @include media('<=medium') {
            .announcements__announcement:nth-child(1),
            .announcements__announcement:nth-child(3) {
                @include greyout();
            }
        }

        .announcements__list {
            left: calc(-#{grid-column-width(6)} - #{2 * $measure-medium});

            @include media('<=medium') {
                left: calc(-#{grid-column-width(6)} - #{$measure-medium});
            }

            @include media('<=min') {
                left: calc(-#{grid-column-width(6)} - #{1 * 0.75rem});
            }
        }

        &[data-announcements='3'] {
            @include media('>medium') {
                .announcements__announcement:nth-child(1) {
                    @include greyout();
                }
            }

            .announcements__list {
                left: calc(-#{grid-column-width(2)} - #{$measure-medium});

                @include media('<=medium') {
                    left: calc(-#{grid-column-width(6)} - #{$measure-medium});
                }

                @include media('<=min') {
                    left: calc(-#{grid-column-width(6)} - #{1 * 0.75rem});
                }
            }
        }
    }

    &[data-page='3'] {
        .announcements__page-controls {
            li:nth-child(3) {
                button {
                    &:before {
                        content: '\F111';
                    }
                }
            }
        }

        &[data-announcements='6'] {
            @include media('>medium') {
                .announcements__announcement:nth-child(4) {
                    @include greyout();
                }
            }
        }

        @include media('<=medium') {
            .announcements__announcement:nth-child(2),
            .announcements__announcement:nth-child(4) {
                @include greyout();
            }
        }

        .announcements__list {
            left: calc(
                -#{grid-column-width(12)} - #{grid-column-width(2)} - #{4 * $measure-medium}
            );

            @include media('<=medium') {
                left: calc(
                    -#{grid-column-width(12)} - #{grid-column-width(2)} - #{2 * $measure-medium}
                );
            }

            @include media('<=min') {
                left: calc(
                    -#{grid-column-width(12)} - #{grid-column-width(2)} - #{2 * 0.75rem}
                );
            }
        }

        &[data-announcements='5'] {
            @include media('>medium') {
                .announcements__announcement:nth-child(3) {
                    @include greyout();
                }
            }

            .announcements__list {
                left: calc(-#{grid-column-width(10)} - #{3 * $measure-medium});

                @include media('<=medium') {
                    left: calc(
                        -#{grid-column-width(12)} - #{grid-column-width(2)} - #{2 *
                            $measure-medium}
                    );
                }

                @include media('<=min') {
                    left: calc(
                        -#{grid-column-width(12)} - #{grid-column-width(2)} - #{2 *
                            0.75rem}
                    );
                }
            }
        }
    }

    &[data-page='4'] {
        .announcements__page-controls {
            li:nth-child(4) {
                button {
                    &:before {
                        content: '\F111';
                    }
                }
            }
        }

        @include media('<=medium') {
            .announcements__announcement:nth-child(3),
            .announcements__announcement:nth-child(5) {
                @include greyout();
            }
        }

        .announcements__list {
            @include media('<=medium') {
                left: calc(
                    -#{grid-column-width(12)} - #{grid-column-width(10)} - #{3 *
                        $measure-medium}
                );
            }

            @include media('<=min') {
                left: calc(
                    -#{grid-column-width(12)} - #{grid-column-width(10)} - #{3 *
                        0.75rem}
                );
            }
        }
    }

    &[data-page='5'] {
        .announcements__page-controls {
            li:nth-child(5) {
                button {
                    &:before {
                        content: '\F111';
                    }
                }
            }
        }

        @include media('<=medium') {
            .announcements__announcement:nth-child(4),
            .announcements__announcement:nth-child(6) {
                @include greyout();
            }
        }

        .announcements__list {
            @include media('<=medium') {
                left: calc(
                    -#{grid-column-width(12)} - #{grid-column-width(12)} - #{grid-column-width(
                            6
                        )} - #{4 * $measure-medium}
                );
            }

            @include media('<=min') {
                left: calc(
                    -#{grid-column-width(12)} - #{grid-column-width(12)} - #{grid-column-width(
                            6
                        )} - #{4 * 0.75rem}
                );
            }
        }
    }

    &[data-page='6'] {
        .announcements__page-controls {
            li:nth-child(6) {
                button {
                    &:before {
                        content: '\F111';
                    }
                }
            }
        }

        @include media('<=medium') {
            .announcements__announcement:nth-child(5) {
                @include greyout();
            }
        }

        .announcements__list {
            @include media('<=medium') {
                left: calc(
                    -#{grid-column-width(12)} - #{grid-column-width(12)} - #{grid-column-width(
                            12
                        )} - #{grid-column-width(2)} - #{5 * $measure-medium}
                );
            }

            @include media('<=min') {
                left: calc(
                    -#{grid-column-width(12)} - #{grid-column-width(12)} - #{grid-column-width(
                            12
                        )} - #{grid-column-width(2)} - #{5 * 0.75rem}
                );
            }
        }
    }

    button[disabled] {
        border: none;
        cursor: default;
    }

    &[data-pages='1'] .announcements__page-controls li:nth-child(2),
    &[data-pages='2'] .announcements__page-controls li:nth-child(3),
    &[data-pages='2'] .announcements__page-controls li:nth-child(3),
    .announcements__page-controls li:nth-child(4),
    .announcements__page-controls li:nth-child(5),
    .announcements__page-controls li:nth-child(6) {
        display: none;

        @include media('<=medium') {
            display: block;
        }
    }

    .announcements__list {
        @include grid;
        @include grid-column-push(2);
        flex-wrap: nowrap !important;
        margin-top: $measure-medium;
        margin: $measure-medium 0 0 0;
        max-width: unset;
        transition-duration: 1s;
        transition-property: left;
        transition-timing-function: cubic-bezier(0.785, 0.135, 0.15, 0.86);

        @include media('<=medium') {
            left: grid-column-width(2);
        }
    }

    .announcements__page-controls__wrapper {
        @include grid;
    }

    .announcements__page-controls {
        @include grid;
        width: unset;
        margin-bottom: $measure-medium;
        margin-left: auto;
        margin-right: auto;
        margin-top: $measure-small;
        justify-content: center;

        li {
            button {
                font-size: $font-size-gamma;

                &:before {
                    @include font-family(icon);
                    content: '\F10C';
                }

                &:hover,
                &:active,
                &:focus {
                    &:before {
                        content: '\F111';
                    }
                }

                &:active {
                    outline: $border-width-default $border-style-alternate
                        $color-black;
                    outline-offset: $border-width-outline;
                }

                &:focus {
                    outline: $border-width-default $border-style-alternate
                        $color-black;
                    outline-offset: 0;
                }

                span {
                    display: none;
                }
            }
        }
    }

    .button-next__wrapper,
    .button-prev__wrapper {
        @include grid;
        height: 85%;
        position: absolute;
        top: 0;
        width: grid-column-width(2);
        z-index: z-index('above');

        button {
            @include anchor('block');
            @include anchor('cta--hub--borderless');
            background-color: $color-hub;
            color: $color-black;
            display: none !important;
            height: $measure-xlarge;
            width: $measure-xlarge;
            align-self: center;

            &:active {
                outline: $border-width-default $border-style-alternate
                    $color-black;
                outline-offset: $border-width-outline;
            }

            &:focus {
                outline: $border-width-default $border-style-alternate
                    $color-black;
                outline-offset: 0;
            }

            &.button-next {
                margin-left: auto;
                margin-right: $measure-large;

                @include media('<=small') {
                    margin-right: $measure-medium;
                }
            }

            &.button-prev {
                margin-left: $measure-large;
                margin-right: auto;

                @include media('<=small') {
                    margin-left: $measure-medium;
                }
            }
        }
    }

    .button-next__wrapper {
        right: 0;
    }

    .button-prev__wrapper {
        left: 0;
    }

    &[data-page='1'] {
        &[data-pages='2'],
        &[data-pages='3'] {
            .button-next {
                display: block !important;
            }
        }
    }

    &[data-page='2'] {
        .button-prev {
            display: block !important;
        }
        &[data-pages='3'] {
            .button-next {
                display: block !important;
            }
        }
    }

    &[data-page='3'] {
        .button-prev {
            display: block !important;
        }
    }

    &[data-page='1'] {
        &[data-announcements='2'],
        &[data-announcements='3'],
        &[data-announcements='4'],
        &[data-announcements='5'],
        &[data-announcements='6'] {
            .button-next {
                @include media('<=medium') {
                    display: block !important;
                }
            }
        }
    }

    &[data-page='2'] {
        .button-prev {
            @include media('<=medium') {
                display: block !important;
            }
        }
        &[data-announcements='3'],
        &[data-announcements='4'],
        &[data-announcements='5'],
        &[data-announcements='6'] {
            .button-next {
                @include media('<=medium') {
                    display: block !important;
                }
            }
        }
    }

    &[data-page='3'] {
        .button-prev {
            @include media('<=medium') {
                display: block !important;
            }
        }
        &[data-announcements='4'],
        &[data-announcements='5'],
        &[data-announcements='6'] {
            .button-next {
                @include media('<=medium') {
                    display: block !important;
                }
            }
        }
    }

    &[data-page='4'] {
        .button-prev {
            @include media('<=medium') {
                display: block !important;
            }
        }
        &[data-announcements='5'],
        &[data-announcements='6'] {
            .button-next {
                @include media('<=medium') {
                    display: block !important;
                }
            }
        }
    }

    &[data-page='5'] {
        .button-prev {
            @include media('<=medium') {
                display: block !important;
            }
        }
        &[data-announcements='6'] {
            .button-next {
                @include media('<=medium') {
                    display: block !important;
                }
            }
        }
    }

    &[data-page='6'] {
        .button-prev {
            @include media('<=medium') {
                display: block !important;
            }
        }
    }

    .announcements__announcement {
        @include box-shadow(delta, rgba(0, 0, 0, 0.15));
        @include grid;
        @include grid-column(4);
        background-color: $color-white;
        color: $color-white;
        margin-right: $measure-medium + $measure-small;
        min-width: grid-column-width(4);
        max-width: grid-column-width(4);
        padding: 0;

        @include media('<=medium') {
            width: grid-column-width(8);
            min-width: grid-column-width(8);
        }

        .announcements__announcement__wrapper {
            width: 100%;

            img {
                width: 100%;
            }
        }

        .announcements__announcement__details {
            @include grid;
            background-image: linear-gradient(
                transparent,
                rgba(12, 12, 14, 0.85) 50%,
                $color-black
            );

            @include media('<=small') {
                background: rgba(12, 12, 14, 0.85);
            }

            height: 100%;
            left: 0;
            margin: 0;
            position: absolute;
            top: 0;
            width: 100%;
            align-content: flex-end;
        }

        .announcements__announcement__details__wrapper {
            color: $color-white;
            padding: $measure-medium;

            .announcements__announcement__title {
                @include anchor('announcement');
                font-family: $font-family-lato;
                font-size: $font-size-delta;
                font-weight: $font-weight-bold;
                margin-bottom: $measure-medium;

                .announcements__announcement__title__text {
                    border-bottom: $border-width-default $border-style-default
                        $color-white;
                }

                .fa {
                    padding-right: $measure-small;
                }

                @include media('<=large') {
                    font-size: $font-size-default;
                }

                @include media('<=medium') {
                    font-size: $font-size-delta;
                }

                @include media('<=between') {
                    font-size: $font-size-small;
                }
            }

            .announcements__announcement__description {
                font-size: $font-size-default;

                @include media('<=large') {
                    font-size: $font-size-small;
                }

                @include media('<=medium') {
                    font-size: $font-size-default;
                }

                @include media('<=between') {
                    font-size: $font-size-small;
                }

                @include media('<=min') {
                    display: none;
                }
            }

            .announcements__announcement__published {
                font-size: $font-size-small;
                margin-top: $measure-small;

                @include media('<=small') {
                    display: none;
                }
            }

            align-self: flex-end;
        }
    }
}
