.feature-events {
    position: relative;
}

.feature-events__cards {
    @include grid;
    padding-left: 1rem;
}

.feature-events__card {
    @include box-shadow(delta, rgba(0, 0, 0, 0.15));

    margin-bottom: $measure-medium;
    margin-right: $measure-medium;
    width: 100%;

    @include media('>between') {
        width: calc(#{33%} - #{$measure-medium});
    }

    a {
        @include anchor(search-result);
    }
}

.feature-events__card {
    .feature-events__card__link {
        height: 100%;

        .feature-events__card__title {
            font-size: $font-size-delta;
            margin-bottom: $measure-small;
            text-align: center;

            @include media('<=between') {
                font-size: $font-size-default;
                text-align: left;
            }
        }

        .feature-events__card__description,
        .feature-events__card__title {
            width: 100%;
        }
    }

    time {
        display: block;
        margin-top: $measure-medium;
        text-align: center;
    }
}

.feature-events__link-more {
    font-family: $font-family-lato;
    font-weight: $font-weight-heavy;

    a {
        @include anchor(skate);

        span {
            font-weight: $font-weight-default;
        }
    }

    position: relative;
    margin: $measure-medium 0 0;
    float: right;
}
