////
/// Buttons
/// @group base
////

a.button {
    display: inline-block;
}

button,
.button {
    @extend %button-elements !optional;
    padding: $measure-small-relative;

    &:focus {
        outline: $border-width-outline $border-style-default $color-black;
    }

    &:active {
        outline: $border-width-default $border-style-alternate $color-black;
    }
}

.button--icon {
    background: $color-hub;
    color: $color-black;

    &:hover {
        background-color: darken($color-hub, 6%);
    }

    span {
        background: transparent;
    }
}
