///
// Left-Hand Navigation
// @group components
///

$level-colors: #f2f2f2, #e5e5e5, #d8d8d8, #cbcbcb, #bebebe, #a1a1a1;

.left-hand-navigation {
    @include box-shadow(beta, rgba($color-black, $opacity-fifth));
    align-self: flex-start;
    font-size: $font-size-small;
    height: fit-content;
    margin-bottom: $measure-medium;
    margin-top: $measure-small;
    max-height: 100vh;
    width: 100%;
    position: sticky;
    top: 0;
    z-index: z-index(navigation);

    h2 {
        background-color: $color-black;
        margin: 0;
        padding: $measure-small $measure-medium;
        font-weight: $font-weight-semibold;
        color: $color-white;
    }

    @include media('<=medium') {
        display: none;
    }

    &.left-hand-navigation--top {
        top: 0;
    }

    &.left-hand-navigation--bottom {
        bottom: 0;
    }

    &.left-hand-navigation--absolute {
        width: calc(100% - #{$measure-medium});
        position: absolute;
    }

    &.left-hand-navigation--fixed {
        position: fixed;

        @include media('<=large') {
            width: calc(
                #{grid-column-width(3)} - #{$measure-medium} - #{$measure-small /
                    2}
            );
        }

        @include media('>large', '<=extralarge') {
            width: calc(
                #{strip-units(grid-column-width(10)) / 100 *
                    grid-column-width(3)} - #{$measure-medium} - #{$measure-small /
                    2}
            );
        }

        @include media('>extralarge') {
            width: calc(
                (#{$grid-width-extra-large} - #{$measure-medium}) * #{strip-units(
                        grid-column-width(10)
                    ) / 100} * #{strip-units(grid-column-width(3)) / 100} - #{$measure-medium}
            );
        }
    }
}

.left-hand-navigation > .left-hand-navigation__list {
    margin-right: $measure-medium;
}

.left-hand-navigation__menu > .vertical-menu--l1 > .vertical-menu__header {
    font-size: $font-size-default;

    & + ul {
        overflow: visible; //So focus outline isn't cut off.
    }
}

.left-hand-navigation-grid {
    @include grid;

    background: $color-white;
    margin-left: auto;
    margin-right: auto;
    max-width: $grid-width-extra-large;

    > div {
        @include grid-column-map(
            (
                'small': 12,
                'large': 10,
            )
        );
        @include grid-column-push-map(
            (
                'small': 0,
                'large': 1,
            )
        );

        > div {
            @include grid;
            background-color: $color-white;
        }
    }
}

.left-hand-navigation-grid__nav {
    width: grid-column-width(3);

    .container {
        height: 100%;
    }
}

.left-hand-navigation-grid__body {
    @include grid-top;
    @include grid-column(7);
    @include grid-column-push(1);
    margin-left: 0;
    margin-right: 0;
    display: initial;

    p {
        width: 100%;
    }

    @include media('<=medium') {
        overflow-x: hidden;
        width: calc(100% + #{$measure-medium});
    }
}

.left-hand-navigation__menu > div > ul {
    max-height: calc(
        100vh - #{$font-size-default * $line-height-perfect-fifth +
            $measure-small * 2 + $measure-small + $measure-medium}
    );
    margin: 0;
    overflow-y: auto;
}

.left-hand-navigation--jsinit {
    h2 {
        display: none;
    }

    .vertical-menu--l2 > .vertical-menu__header > .vertical-menu__page-link {
        padding: $measure-small $measure-medium;
        position: relative;
        z-index: z-index(default);
    }
}
