.hallway {
    @extend .section--padded;
}

.hallway__item > a {
    @include anchor(hallway, '.hallway__item__title > span');
}

.hallway__item__title {
    margin-top: $measure-medium;
    margin-bottom: $measure-medium;

    font-size: $font-size-delta;
    font-weight: 600;
    font-family: $font-family-lato;
}

.hallway__item__text {
    @include media('<=medium') {
        margin-bottom: $measure-large;
    }
}
