.noticeboard__list {
    @include grid;
    padding-left: 1rem;
    padding-top: 1rem;
}

.noticeboard__notice {
    @include box-shadow(delta, rgba(0, 0, 0, 0.15));

    background-color: $color-white-smoke;
    margin-bottom: $measure-medium;
    margin-right: $measure-medium;
    width: grid-column-width(12);

    @include media('>medium') {
        width: calc(#{50%} - #{$measure-medium});
    }

    &:hover,
    &:focus,
    &:active {
        box-shadow: 0 0 1.5rem 3px rgba(0, 0, 0, 0.15);
    }
}

.noticeboard__notice__close {
    @include anchor(skate);
    top: 0;
    display: none;
    font-family: $font-family-lato;
    font-weight: $font-weight-heavy;
    padding-top: $measure-small;
    padding-right: $measure-medium;
    right: 0;
    position: absolute;
    text-transform: uppercase;
}

.noticeboard-finder {
    .noticeboard__notice {
        &.active {
            .noticeboard__notice__close {
                display: block;
            }

            .noticeboard__notice__details {
                display: block;
            }
        }
    }
}

.noticeboard__notice__wrapper {
    @include grid;
    padding-bottom: $measure-medium;
    padding-left: $measure-medium;
    padding-right: $measure-medium;
    padding-top: $measure-small;
}

.noticeboard__notice__main {
    @include grid;

    .noticeboard__notice__published {
        width: grid-column-width(3);
        font-family: $font-family-lato;
        font-size: $font-size-default;
        font-weight: $font-weight-heavy;
        padding: $measure-medium;

        .noticeboard__notice__published__wrapper {
            background-color: $color-hub;
            margin-left: auto;
            margin-right: auto;
            max-width: grid-column-width(9);
            padding: $measure-small;
            text-align: center;

            @include media('<=small') {
                max-width: 100%;
            }
        }

        .noticeboard__notice__day {
            display: block;
            font-size: $font-size-beta;

            @include media('<=small') {
                font-size: $font-size-delta;
            }
        }

        .noticeboard__notice__month {
            display: block;
            font-size: $font-size-small;
            text-transform: uppercase;
        }
    }

    .noticeboard__notice__top {
        padding: $measure-small;
        width: grid-column-width(9);
        align-self: center;

        .noticeboard__notice__category {
            font-size: $font-size-small;
            margin-bottom: $measure-medium;

            a {
            }
        }

        .noticeboard__notice__title {
            @include anchor(monochrome);
            font-family: $font-family-lato;
            font-size: $font-size-delta;
            font-weight: $font-weight-heavy;

            @include media('<=small') {
                font-size: $font-size-delta;
            }
        }
    }
}

.noticeboard__notice__details {
    @include grid-column(9);
    @include grid-column-push(3);

    border-top: $border-width-default $border-style-default $color-lucifer;
    display: none;

    .noticeboard__notice__description {
        padding: $measure-large 0 $measure-medium 0;
    }

    .noticeboard__notice__contact {
        text-align: right;

        a {
        }
    }
}

.no-js {
    .noticeboard__notice__details {
        display: block;
    }
}

.noticeboard__empty {
    @include grid;

    .noticeboard__empty__message {
        text-align: center;
        width: 100%;
    }
}

.noticeboard-finder {
    margin-top: -#{$measure-medium * 2};
    overflow-x: hidden;

    .noticeboard-finder__wrapper {
        @include grid;
        min-height: 15.5rem;
    }
}

.noticeboard-finder {
    .noticeboard-finder__search-form {
        @include grid-column(6);
        @include grid-column-push(3);

        @include media('<=large') {
            @include grid-column(10);
            @include grid-column-push(1);
        }

        @include media('<=medium') {
            @include grid-column(12);
        }

        align-self: center;
    }

    .noticeboard__notice {
        @include media('>medium') {
            width: grid-column-width(12);
        }
    }

    .noticeboard-finder__top {
        @include grid;
        min-height: 10.5rem;

        @include media('<=medium') {
            margin-left: $measure-small;
        }
    }

    .noticeboard-finder__search-form {
        display: block;
        margin: 3.5rem $measure-small 0;
        position: relative;
        text-align: left;
    }

    .query-box__label {
        color: $color-white;
        display: block;
        font-family: $font-family-lato;
        font-size: $font-size-default;
        font-weight: $font-weight-semibold;
        margin-left: $measure-small;
    }

    .query-box__query {
        font-family: $font-family-default;
        font-size: $font-size-default;
        font-weight: $font-weight-default;
        height: $measure-xlarge;
        margin: 0 0 0 $measure-small;
        padding: $measure-small;
        vertical-align: top;
        width: grid-column-width(10);

        @include media('<=min') {
            width: grid-column-width(9);
        }

        &:focus {
            outline: $border-width-default $border-style-default $color-hub;
        }
    }

    .query-box__button {
        @extend .button--icon;
        width: $measure-xlarge;
        height: $measure-xlarge;
        vertical-align: top;

        span:before {
            @include font-family(icon);
            content: $fa-var-search;
            font-size: $font-size-gamma;
            font-weight: $font-weight-bold;
            line-height: $font-size-gamma;
        }
    }

    .noticeboard-finder__main {
        @include grid;
        background-color: $color-gallery;
        padding-bottom: $measure-large;
    }

    .noticeboard-finder__main__left {
        @include grid-column(2);
        @include grid-column-push(1);
    }

    .noticeboard-finder__main__right {
        @include grid-column(7);
        @include grid-column-push(2);
    }

    .noticeboard-finder__results__count {
        margin-bottom: $measure-xlarge;
        padding-left: $measure-medium;
        text-align: center;
        width: 100%;

        span span {
            font-weight: $font-weight-semibold;
        }
    }

    .noticeboard-finder__results__refresh {
        display: inline-block;
        color: $color-hub;
        margin-left: $measure-small;
    }

    .fade-out {
        opacity: 0;
        pointer-events: none;
        user-select: none;
        animation: fade-out 1s linear;
    }

    .noticeboard-finder__results {
        @include grid;
        padding-left: $measure-medium + $measure-small;
        padding-right: $measure-medium + $measure-small;

        @include media('<=medium') {
            padding-left: $measure-medium + $measure-small;
            padding-right: $measure-medium + $measure-small;
        }
    }

    .noticeboard-finder__results__list {
        @include grid;
        padding-left: 1rem;
    }
}
