////
/// URL Dropdown
/// @group components
////

.url-slide {
    a {
        @include anchor(sliding--right);
    }

    .no-js & {
        @include visually-hidden;
    }
}

.url-slide--right {
    .url-slide__icon {
        @extend .fa;
        @extend .fa-angle-right;
        margin-right: $measure-small;
    }

    .url-slide__current {
        margin-right: $measure-small;
    }
}
