////
/// Intro
/// @group components
////

.intro {
    position: relative;

    @include media('<=medium') {
        padding-top: $measure-large;
        padding-bottom: $measure-large;
        margin-top: $measure-large;
        margin-bottom: $measure-large;
    }

    &:before {
        @include absolute(top-full);
        content: ' ';
        background-color: $color-black;
        height: 100%;
        display: block;
        z-index: z-index(below);

        @include media('>medium') {
            height: 80%;
            left: #{$measure-large * 2};
        }

        @include media('>large') {
            left: calc(
                (100% - #{$grid-width-extra-large}) / 2 + 2 * #{$measure-large}
            );
        }
    }
}

.intro--left-video {
    .intro__oversize-media {
        @include media('>medium') {
            margin-top: #{$measure-large * 2};
        }
    }

    & > .container > .grid {
        & > :nth-child(1) {
            @include grid-column(7);
        }

        & > :nth-child(2) {
            @include grid-column(4);
            @include grid-column-push(1);
        }
    }

    .caption {
        @include font-family(lato);
        align-self: flex-start;
        align-items: flex-start;
        font-style: normal;

        .caption__type {
            text-transform: uppercase;
        }
    }

    @include media('<=medium') {
        figcaption {
            color: $color-white;
        }

        & > .container > .grid {
            & > :nth-child(1) {
                order: 2;
            }

            & > :nth-child(2) {
                order: 1;
            }
        }
    }
}
