.block-links__bg {
    background-color: $color-lightgray;
    position: relative;

    &.img-bg-section {
        background-color: $transparent;
        &:before {
            @include absolute(top-left);
            content: '';
            background-color: rgba($color-black, $opacity-half);
            width: 100%;
            height: 100%;
            z-index: z-index(default);
        }
    }
}

.block-links__bg--thin {
    padding: $measure-large $measure-medium;
    margin-bottom: $measure-large;

    &.img-bg-section {
        padding: #{$measure-medium * 4} $measure-medium;
    }
}

.block-links__items {
    color: $color-white;
    width: auto;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-around;
    opacity: 0.95;
    position: relative;
    z-index: z-index(above);

    @include media('<=between') {
        margin: $measure-large $measure-small;
    }

    @include media('>between') {
        margin: $measure-enormous $measure-small;
    }

    .block-links__bg--thin & {
        margin: 0;
    }
}

.block-links__items--thin {
    margin: 0;
}

.block-links__items--blue {
    background-color: $color-prussian;
}

.block-links__items--red {
    background-color: $color-alizarin;
}

.block-links__items--purple {
    background-color: $color-vivid;
}

$level-colors: (
    undergraduate: $color-flamingo,
    masters: $color-prussian,
    mba: $color-sherpa,
    phd: $color-allports,
);

@each $level, $color in $level-colors {
    .block-links__items--#{$level} {
        background-color: #{$color};
    }
}

.block-links__items__anchor {
    @include media('<=small') {
        width: grid-column-width(12);

        &:after {
            content: '';
            background-color: #a1a1a1;
            height: $border-width-default;
            display: block;
            position: absolute;
            bottom: 0;
            left: $measure-medium;
            right: $measure-medium;
        }
    }

    @include media('>small', '<=medium') {
        width: grid-column-width(6);
    }

    @include media('>medium') {
        width: grid-column-width(4);
    }

    .left-hand-navigation-grid__body & {
        @include media('<=small') {
            width: grid-column-width(12);
        }

        @include media('>small') {
            width: grid-column-width(6);
        }
    }
}

.block-links__items__anchor__header {
    padding-top: $measure-medium;
    padding-bottom: $measure-medium;
    overflow: hidden;
    word-break: break-word;

    @include media('<=medium') {
        padding-top: $measure-small;
        padding-bottom: $measure-small;
        padding-right: #{$measure-medium + $measure-small};
        margin-bottom: 0;
        font-size: $font-size-delta;
    }

    @include media('>medium') {
        padding-right: $measure-large;
        font-size: $font-size-gamma;
    }
}

.block-links__items__anchor__text {
    @include media('<=medium') {
        display: none;
    }
}

.block-links__items__anchor__header__icon {
    display: block;
    position: absolute;
    right: 0;
    font-weight: $font-weight-light;

    @include media('<=medium') {
        top: #{$measure-medium - $measure-small / 2};
    }

    @include media('>medium') {
        top: #{$measure-medium + $measure-small / 2};
    }
}
