@mixin filter-field($active-bg: true) {
    background: $color-white;
    border-radius: 0;
    border: $border-width-hairline $border-style-default $color-lucifer;
    font-family: $font-family-default;
    font-size: $font-size-default;
    height: $measure-xlarge;
    padding: $measure-small;

    &:focus {
        outline: $border-width-default $border-style-default $color-hub;
    }

    @if $active-bg {
        &:hover,
        &:focus,
        &:active {
            background: $color-gallery;
        }
    }
}

/******* mobile floating filters ***********/

.filter-box {
    position: relative;

    h2 {
        font-size: $font-size-delta;
        margin: 0;
    }
}

.filter-box__open {
    border-bottom: none;
    display: block;
    padding: $measure-small $measure-medium;

    &:link,
    &:visited {
        background: $color-hubsecondary;
        color: $color-white;
    }

    &:hover {
        background: darken($color-hubsecondary, 6%);
        color: $color-white;
    }

    &:active {
        outline: $border-width-default $border-style-alternate $color-black;
        outline-offset: 0;
    }

    &:focus {
        outline: $border-width-default $border-style-alternate $color-black;
        outline-offset: $border-width-outline;
    }

    @include media('>between') {
        display: none;
    }

    .fa {
        float: right;
        padding-top: $measure-small;
    }
}

.filter-box__float {
    overflow: hidden;
    height: 0;
    margin-bottom: $measure-medium;

    &.filter-box__float--open,
    &.filter-box__float--fixed {
        height: auto;
    }

    @include media('>between') {
        &,
        &.filter-box__float--open,
        &.filter-box__float--fixed {
            height: auto;
        }
    }
}

.filter-box__inner {
    border: $border-style-default $color-gallery;
    border-width: 0 $border-width-hairline $border-width-hairline;
    padding: $measure-medium $measure-medium 0;
    background: white;

    h2 {
        display: none;
    }

    @include media('>between') {
        padding: $measure-small $measure-medium;
        border: none;

        h2 {
            display: block;
        }
    }
}

.filter-box__float--fixed .filter-box__inner {
    padding-top: $measure-medium;
    h2 {
        display: block;
    }
}

.filter-box__filter {
    margin-bottom: $measure-medium;
}

.filter-box__filter__header a {
    display: block;
}

.filter-box__filter__header .fa {
    display: none;
}

.js {
    .filter-box__filter__header {
        cursor: pointer;
    }

    .filter-box__filter__header .fa {
        display: block;
        float: right;
    }

    .filter-box__filter__content--closed {
        display: none;
    }
}

.filter-box__filter__content {
    label {
        display: block;
        cursor: pointer;
    }

    select {
        width: 100%;

        @include filter-field;
    }

    input {
        position: relative;
        top: $measure-small;
    }
}
