////
/// Accordion
/// @group components
////

.js {
    .accordion__content__heading-anchor {
        &.inactive + div {
            @include visually-hidden;
        }
    }

    .accordion__content__heading {
        @include visually-hidden;
    }
}

.accordion {
    margin-bottom: $measure-large;
    margin-top: $measure-large;
    width: 100%;
}

.accordion__content__heading-anchor__icon {
    @extend .fa;
    float: right;

    .accordion__content__heading-anchor.inactive & {
        @extend .fa-plus;
    }

    .accordion__content__heading-anchor.active & {
        @extend .fa-minus;
    }
}

.accordion__content__heading-anchor {
    @include anchor(activable-stacked-normal--transparent);
    h2,
    h3,
    h4 {
        margin: 0;
    }
    padding-left: $measure-medium;
    padding-right: $measure-medium;
}

.accordion__content__heading-anchor__label {
    pointer-events: none;
}

.accordion__content__group {
    padding: $measure-medium;
}

.accordion.accordion--simple {
    border-bottom: $border-width-default $border-style-alternate $color-quill;
    margin-bottom: $measure-large;

    .accordion__content__heading-anchor {
        @include anchor(activable-stacked-simple);
        padding-left: 0;

        .accordion__content__heading-anchor__icon {
            @extend .fa;
            float: left;
            margin-right: $measure-medium;
        }

        &.inactive {
            .accordion__content__heading-anchor__icon {
                @extend .fa-plus;
            }
        }

        &.active {
            .accordion__content__heading-anchor__icon {
                @extend .fa-times;
            }
        }
    }

    .accordion__content__group {
        border-top: $border-width-default $border-style-alternate $color-quill;
        padding: $measure-medium $measure-small $measure-small $measure-small;
    }
}
