////
/// Tooltips
/// @group components
////

[data-tooltip] {
    border-bottom: $border-width-hairline $border-style-alternate currentColor;
    position: relative;

    &:after {
        @include absolute(bottom-left, 100%);
        @include transition(opacity);
        content: attr(data-tooltip);
        opacity: 0;
        background-color: $color-white;
        color: $color-black;
        display: block;
        padding: $measure-small;
        border: $border-width-hairline $border-style-default currentColor;
    }

    &:hover {
        &:after {
            opacity: 1;
        }
    }
}
