////
/// External Image Link Strip
/// @group components
////

.external-img-link-strip {
    @include grid;

    text-align: center;

    & > a,
    & > img,
    & > div {
        display: inline-block;
    }

    & > a {
        @include anchor(external-img);
    }

    @supports (display: flex) {
        justify-content: center;

        & > a,
        & > img,
        & > div {
            // this Anchor Type has internal conditions to determine anchor|not
            @include grid-column-base;

            @include media('>large') {
                width: grid-column-width(2);
            }

            @include media('<=large', '>medium') {
                width: grid-column-width(3);
            }

            @include media('<=medium', '>small') {
                width: grid-column-width(4);
            }

            @include media('<=small') {
                width: grid-column-width(6);
            }
        }
    }
}
