////
/// Key Info
/// @group components
////

.key-info {
    @include grid;
    margin-top: $measure-large;
    position: relative;

    @supports (display: flex) {
        justify-content: center;
    }
}
.key-info--lined {
    margin-top: 0;
    padding-top: $measure-large;
    padding-bottom: $measure-large;
    border-top: $border-width-hairline $border-style-default $color-emperor;
    border-bottom: $border-width-hairline $border-style-default $color-emperor;

    // alternate colors
    &.key-info--phd {
        border-color: $color-allports;
    }
}

.key-info__item {
    @include grid-column-map(
        (
            'small': 6,
            'medium': 4,
            'large': 3,
        )
    );

    @include media('>large') {
        width: auto;
        padding-left: $measure-large;
        padding-right: $measure-large;
    }

    // items 4+
    &:nth-child(1n + 4) {
        @include media('<=large') {
            margin-top: $measure-large;
        }
    }

    // item 3
    &:nth-child(3) {
        @include media('<=medium') {
            margin-top: $measure-large;
        }
    }

    // items 2+
    &:nth-child(1n + 2) {
        @include media('<=small') {
            margin-top: $measure-medium;
        }
    }
}

.key-info--with-placement {
    .key-info__item {
        @include media('>large') {
            width: 25%;
        }

        &:nth-child(5) {
            @include media('>large') {
                margin-left: 25%;
            }
        }

        // items 5+
        &:nth-child(1n + 5) {
            @include media('>large') {
                margin-top: $measure-large;
            }
        }
    }
}

.key-info__item__prop {
    color: $color-emperor;
}

.key-info__item__value {
    color: $color-flamingo;
    font-size: 18px;
    font-weight: $font-weight-bold;

    // alternate colors
    .key-info--phd & {
        color: $color-allports;
    }
}

.key-info__item--feature {
    align-items: center;
    display: flex;

    .key-info__item__prop,
    .key-info__item__value {
        display: inline-block;
    }

    .key-info__item__prop {
        font-weight: $font-weight-bold;
        text-transform: uppercase;
        margin-right: $measure-small;
        color: lighten($color-emperor, 15%);
    }
}

.key-info--2-col {
    margin-top: $measure-large;

    @include media('>medium') {
        margin-top: 0;
    }

    .key-info__item--iconed {
        display: block;
        margin-bottom: $measure-large;

        @include media('>small') {
            vertical-align: text-top;
            display: inline-block;
            width: 45%;
            margin-right: $measure-medium;
        }
    }
}

.key-info--dark-background {
    .key-info__item__prop,
    .key-info__item__value {
        color: $color-white;
    }
}

.key-info--dynamic {
    .no-js & {
        display: none;
    }
}

.key-info__item--iconed {
    .fa {
        display: inline-block;
        min-width: $measure-large;
        font-size: $font-size-delta;
    }

    .key-info__item__prop {
        span:not([class]) {
            vertical-align: top;
            line-height: $line-height-small;
        }
    }

    .key-info__item__value {
        padding-left: $measure-large;
    }
}
