$cutout-height: 4rem;

.banner--image {
    @include media('<=between') {
        .banner__image__group {
            picture {
                position: relative;
            }
            .banner__container {
                padding: 0;
                .banner__content {
                    padding: 0;
                    .banner__content__text {
                        .banner__content__text__item {
                            display: inline-block;
                            background-color: $color-black;
                        }
                    }
                }
            }
            .banner__over-image--no-img-bg {
                padding: $measure-medium
                    calc(#{$measure-large} - #{$measure-small}) 0
                    $measure-medium;
                .banner__container {
                    .banner__content__text {
                        padding: 0;
                        margin-bottom: 0;
                        .banner__content__text__item {
                            margin-bottom: $measure-small;
                        }
                    }
                }
            }
        }
    }

    .banner__container,
    .banner__content {
        height: 100%;
    }

    .banner__content__text {
        margin-bottom: 3 * $measure-large;
    }

    .banner__over-image--no-img-bg {
        .banner__content__text {
            margin-bottom: $measure-medium;
        }
    }

    .banner__content__text__standfirst {
        color: $color-white;
    }

    .banner__content__ctas {
        text-align: center;

        a {
            @include anchor(draw-outline, $color-white);
            margin: 0 $measure-medium $measure-large;
        }
    }

    &.banner--image--with-promo .banner__content__ctas {
        @include media('>medium') {
            text-align: left;
        }
        @include media('>large') {
            text-align: center;
        }
    }

    @include media('<=medium') {
        margin-bottom: $measure-large;
    }

    @include media('>medium') {
        &.banner--cutout--left,
        &.banner--cutout--right {
            margin-bottom: -1px;

            .banner__content {
                margin-bottom: $cutout-height * 2;
            }
            .banner__content {
                margin-bottom: $cutout-height * 3;
            }

            .banner__content__ctas {
                bottom: $measure-xlarge;
            }

            .banner__standfirst {
                margin-top: -$cutout-height / 2;
                position: relative;
                z-index: z-index(above);
            }
        }
    }
}

.banner__image__group {
    @extend .img-bg-section;

    @include media('>medium') {
        .banner--cutout--left &:after,
        .banner--cutout--right &:after {
            content: '';
            background-color: $color-white;
            width: calc(100% - #{$cutout-height});
            height: $cutout-height;
            position: absolute;
            bottom: 0;
            z-index: z-index(above);
        }

        .banner--cutout--left &:after {
            left: 0;
        }

        .banner--cutout--right &:after {
            right: 0;
        }
    }
}

.banner__img {
    height: 100%;

    &.banner__img__new {
        @include media('>between') {
            max-height: 520px;
        }
        .banner__over-image {
            &:not(.banner__over-image--no-img-bg) {
                @include media('>between') {
                    position: absolute;
                    top: 0;
                    width: 100%;
                }
                .banner__content__text {
                    padding: 0;
                    margin: 0;
                }
            }
        }
    }

    &:not(.banner__img__new) {
        + .banner__over-image:not(.banner__over-image--no-img-bg) {
            .banner__content__text {
                margin-bottom: 10rem;
            }
        }
    }

    &.banner__img__new
        + .banner__over-image:not(.banner__over-image--no-img-bg) {
        .banner__container .banner__content {
            .banner__content__text {
                @include media('<=medium') {
                    margin-bottom: 10%;
                }
                @include media('<=between') {
                    padding: 0;
                    margin: 0;
                    .banner__content__text__item {
                        display: block;
                    }
                }
            }
        }
    }

    > img,
    > picture {
        position: absolute;
        top: 0;
        left: 50%;
        transform: translateX(-50%);
        min-width: 100%;
        max-width: none;
        min-height: 100%;
        > img {
            min-width: 100%;
        }
    }

    .banner--full-overlay &,
    .banner--partial-overlay & {
        &:after {
            display: block;
            content: '';
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            background: black;
            opacity: 0.8;
        }
    }

    .banner--partial-overlay & {
        &:after {
            background: linear-gradient(0deg, $color-black, 70%, transparent)
                no-repeat bottom;
            background-size: 100% 70%;
        }
    }
}

.banner__img__none {
    background-color: $color-hubsecondary;
    position: absolute;
    top: 0;
    left: 50%;
    transform: translateX(-50%);
    min-width: 100%;
    max-width: none;
    min-height: 100%;
    width: calc(100% + #{$measure-medium});
}

.banner--cutout--none {
    margin-bottom: $measure-large;
}

.banner__over-image {
    @include media('<=small') {
        position: relative;
        top: -1px;
    }
}
