////
/// Animations Mixins
/// @group abstracts
////

///
/// Animation
///
/// @author Chris Burnell
///
/// @param {String} $values - Values
///
/// @example scss - One parameter
///   @include animation(fade-in);
///   // opacity: 0;
///   // animation: fade-in 0.2s ease 0s both normal;
///
/// @example scss - One animation, activated on a class
///   @include animation(fade-in, ".class");
///   // opacity: 0;
///   // &.class {
///   //   animation: fade-in 0.2s ease 0s both normal;
///   // }
///
/// @example scss - One animation, activated on a class, without the before state
///   @include animation(false, fade-in, ".class");
///   // &.class {
///   //   animation: fade-in 0.2s ease 0s both normal;
///   // }
///
@mixin animation($values...) {
    $include-pre: true;
    $event: null;
    $animation: ();

    @if $values == 'none' {
        $animation: none;
    } @else if str-index(#{$values}, ',') {
        @each $value in $values {
            @if is-boolean($value) {
                @if $value == false or $value == 'false' {
                    $include-pre: false;
                }
            } @else if is-event($value) or is-class($value) {
                $event: $value;
            } @else {
                $single-animation: unpack-animation($value);
                @if $include-pre {
                    @include animation-pre($single-animation);
                }
                $animation: append($animation, $single-animation, comma);
            }
        }
    } @else {
        $single-animation: unpack-animation($values...);
        @include animation-pre($single-animation);
        $animation: $single-animation;
    }

    @if $event {
        @if str-index(#{$event}, '&') {
            #{$event} {
                animation: #{$animation};
            }
        } @else {
            &#{$event} {
                animation: #{$animation};
            }
        }
    } @else {
        animation: #{$animation};
    }
}

@mixin animation-pre($list) {
    $name: nth($list, 1);
    $is-forwards: if(index($list, reverse), false, true);
    $is-fade: if(str-index($name, fade), true, false);
    $is-slide: if(str-index($name, slide), true, false);
    $is-rotation: if(str-index($name, rotate), true, false);
    $is-scale: if(str-index($name, scale), true, false);
    @if $is-fade {
        @if $is-forwards {
            opacity: 0;
        } @else {
            opacity: 1;
        }
    } @else if $is-slide and $is-forwards {
        @if str-index($name, top) {
            transform: translateY(-105%);
        } @else if str-index($name, bottom) {
            transform: translateY(105%);
        } @else if str-index($name, right) {
            transform: translateX(105%);
        } @else if str-index($name, left) {
            transform: translateX(-105%);
        }
    } @else if $is-rotation and $is-forwards {
        @if str-index($name, vertical) {
            transform: rotateX(-90deg);
        } @else {
            transform: rotateY(-90deg);
        }
    } @else if $is-scale and $is-forwards {
        @if str-index($name, vertical) {
            transform: scaleY(0);
        } @else {
            transform: scaleX(0);
        }
    } @else {
        transform: none;
    }
}
