////
/// Indicator
/// @group components
////
.indicator {
    bottom: 0;
    top: unset;
    position: absolute;
    text-align: center;
    width: 100%;

    .indicator__wrapper {
        @include grid;
        @include anchor(indicator);
        @include box-shadow(delta, rgba(0, 0, 0, 0.15));

        background-color: rgba(255, 255, 255, 0.8);
        border-radius: 100%;
        color: $color-black;
        display: inline-block;
        height: $measure-xlarge;
        margin-left: auto;
        margin-right: auto;
        padding: $measure-medium;
        width: $measure-xlarge;
    }

    span {
        display: block;
        margin-top: -0.25rem;
    }

    span + span {
        margin-top: -0.5rem;
    }

    &.indicator--top {
        bottom: unset;
        top: 0;
    }
}

.service-finder-nest {
    .indicator {
        margin-left: -$measure-small;
    }
}

.pocket-cards {
    .indicator {
        margin-left: -$measure-large;
    }
}

.noticeboard-nest {
    .indicator {
    }
}
