////
/// No Scroll
/// @group components
////

/*html*/
.no-scroll {
    overflow-y: hidden;
}

@each $name, $value in $breakpoints {
    .no-scroll--below-#{$name} {
        @include media('<=#{$name}') {
            overflow-y: hidden;
        }
    }
    .no-scroll-above-#{$name} {
        @include media('>#{$name}') {
            overflow-y: hidden;
        }
    }
}
