////
/// Popup Definition List Group
/// @group components
////

.popup-def-list-group {
    margin-bottom: $measure-large;
}

.js .popup-def-list-group {
    .popup-def-list-group__content {
        @include visually-hidden;
    }

    .popup-def-list-group__anchor {
        @include anchor(inline);
    }
}

.js .popup-def-list-group li {
    > h5,
    > h4,
    > h3,
    > h2 {
        display: inline;
        font-size: $font-size-default;
    }
}

.popup-def-list-group__dlg__title__small {
    text-transform: uppercase;
    font-size: $font-size-default;
    padding-bottom: $measure-medium;
}

.popup-def-list-group__prevnext {
    margin-top: 3rem;
    a {
        @include anchor(skate);
    }
}

.popup-def-list-group__prevnext__next,
.popup-def-list-group__prevnext__prev {
    display: inline-block;
    width: 50%;
    font-weight: $font-weight-semibold;
    position: relative;
    color: $color-lucifer;
}

.popup-def-list-group__prevnext__prev {
    text-align: left;
    border-right: 2px solid $color-lucifer;
    padding-left: 26px;
    padding-right: $measure-small;

    &:before {
        display: block;
        margin-bottom: $measure-small;
        content: 'PREVIOUS';
        font-weight: $font-weight-thin;
    }
    .popup-def-list-group__prevnext__prev__icon {
        @extend .fa;
        @extend .fa-arrow-left;
        margin-right: $measure-small;
        position: absolute;
        bottom: 10px;
        left: 0;
    }
}

.popup-def-list-group__prevnext__next {
    text-align: right;
    border-left: 2px solid transparent;
    padding-right: 26px;
    padding-left: $measure-small;

    &:before {
        display: block;
        margin-bottom: $measure-small;
        content: 'NEXT';
        font-weight: $font-weight-thin;
    }

    .popup-def-list-group__prevnext__next__icon {
        @extend .fa;
        @extend .fa-arrow-right;
        margin-left: $measure-medium;
        position: absolute;
        bottom: 10px;
    }
}

.popup-def-list-group__dlg.popup-def-list-group__dlg--yellow {
    .popup-def-list-group__prevnext__prev {
        border-right: 2px solid $color-yellow;
    }
}

.popup-def-list-group__dlg.popup-def-list-group__dlg--allports {
    .popup-def-list-group__prevnext__prev {
        border-right: 2px solid $color-allports;
    }
}
