.banner__content__video__grid {
    @include grid;
}

.banner--video {
    position: relative;
    padding-top: $measure-large;
    padding-bottom: $measure-large;

    &:not(.banner--video--imgbg) {
        background: gradient(0deg, #2f2f2f, #2f2f2f) no-repeat;
        @include media('<=medium') {
            background-size: 100% 60%;
        }

        @include media('>medium') {
            background-size: calc(100% - 2 * #{$measure-large}) 60%;
        }

        @include media('>extralarge') {
            background-size: calc(
                    50% + #{$grid-width-large} / 2 - 2 * #{$measure-large}
                )
                60%;
        }
    }

    .banner__imgbg {
        @include media('<=medium') {
            display: none;
        }

        position: absolute;
        top: 0;
        left: 0;
        min-width: 100%;
        width: auto;
        height: 60%;
        overflow: hidden;

        img {
            min-width: 100%;
            min-height: 100%;
            width: auto;
            height: auto;
        }

        &:after {
            display: block;
            content: '';
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            background: rgba(0, 0, 0, 0.9);
        }
    }

    .banner__content__ctas {
        margin-top: $measure-enormous;
        a {
            @include anchor(cta);
            @include anchor(cta--inline);
            @include anchor(cta--allports);

            margin: 0 $measure-large $measure-large 0;
        }
    }

    .banner__content .banner__content__text {
        @include media('<=medium') {
            margin-top: $measure-medium;
            margin-bottom: $measure-medium;
            padding-top: 0;
        }
    }
}

.banner__content__video {
    @include grid-column(6);
}
