.service-finder {
    margin-top: -#{$measure-medium * 2};
    overflow-x: hidden;

    .service-finder__wrapper {
        @include grid;
        min-height: 15.5rem;
    }
}

.service-finder,
.service-finder-nest {
    .service-finder__search-form {
        @include grid-column(6);
        @include grid-column-push(3);

        @include media('<=large') {
            @include grid-column(10);
            @include grid-column-push(1);
        }

        @include media('<=medium') {
            @include grid-column(12);
        }

        @supports (display: flex) {
            align-self: center;
        }
    }

    .service-finder__top {
        @include grid;
        min-height: 10.5rem;
    }

    .service-finder__search-form {
        display: block;
        margin: 3.5rem $measure-small 0;
        position: relative;
        text-align: left;
    }

    .query-box__label {
        color: $color-white;
        display: block;
        font-family: $font-family-lato;
        font-size: $font-size-default;
        font-weight: $font-weight-semibold;
        margin-left: $measure-small;
    }

    .query-box__query {
        font-family: $font-family-default;
        font-size: $font-size-default;
        font-weight: $font-weight-default;
        height: $measure-xlarge;
        margin: 0 0 0 $measure-small;
        padding: $measure-small;
        vertical-align: top;
        width: grid-column-width(10);

        @include media('<=min') {
            width: grid-column-width(9);
        }

        &:focus {
            outline: $border-width-default $border-style-default $color-hub;
        }
    }

    .query-box__button {
        @extend .button--icon;
        width: $measure-xlarge;
        height: $measure-xlarge;
        vertical-align: top;

        span:before {
            @include font-family(icon);
            content: $fa-var-search;
            font-size: $font-size-gamma;
            font-weight: $font-weight-bold;
            line-height: $font-size-gamma;
        }
    }

    .service-finder__main {
        @include grid;
        background-color: $color-gallery;
        padding-bottom: $measure-large;
    }

    .service-finder__results__count {
        @include grid-column(6);
        @include grid-column-push(3);

        margin-bottom: $measure-xlarge;
        padding-left: $measure-medium;
        width: 100%;

        @include media('<=large') {
            @include grid-column(10);
            @include grid-column-push(1);
        }

        @include media('<=medium') {
            @include grid-column(12);
        }

        span span {
            font-weight: $font-weight-semibold;
        }
    }

    .service-finder__results__refresh {
        display: inline-block;
        color: $color-hub;
        margin-left: $measure-small;
    }

    .fade-out {
        opacity: 0;
        pointer-events: none;
        user-select: none;
        animation: fade-out 1s linear;
    }

    .service-finder__results {
        @include grid;
        padding-left: $measure-medium + $measure-small;
        padding-right: $measure-medium + $measure-small;

        @include media('<=medium') {
            padding-left: $measure-medium + $measure-small;
            padding-right: $measure-medium + $measure-small;
        }
    }

    .service-finder-result {
        @include box-shadow(delta, rgba(0, 0, 0, 0.15));

        margin-bottom: $measure-medium;
        margin-right: $measure-medium;
        width: 100%;

        @include media('>between') {
            width: calc(#{50%} - #{$measure-medium});
        }

        @include media('>medium') {
            width: calc(#{33%} - #{$measure-medium});
        }

        @include media('>large') {
            width: calc(#{25%} - #{$measure-medium});
        }

        a {
            @include anchor(search-result);
        }

        &:hover,
        &:focus,
        &:active {
            box-shadow: 0 0 1.5rem 3px rgba(0, 0, 0, 0.15);
        }
    }

    .service-finder-result__icon {
        display: block;
        width: 100%;
        height: $measure-xlarge;

        img {
            height: 100%;
            margin: 0 auto;
        }

        @include media('<=between') {
            height: unset;

            img {
                height: $measure-xlarge;
            }
        }
    }

    .service-finder-result__title {
        font-size: $font-size-delta;
        margin-bottom: $measure-small;
    }

    .service-finder-result {
        .service-finder-result__link {
            height: 100%;

            .service-finder-result__description,
            .service-finder-result__title {
                text-align: center;
                width: 100%;
            }

            .service-finder-result__description {
                font-size: $font-size-small;
            }
        }
    }

    .service-finder__results__list {
        @include grid-top;
    }

    .service-finder-result__url {
        font-size: $font-size-small;
        font-style: italic;
    }
}

.service-finder {
    .service-finder__top {
        margin-left: $measure-small;
        margin-right: $measure-small;
    }

    .service-finder__main {
        @include media('<=medium') {
            margin-left: $measure-small;
        }
    }
}
