////
/// Media
/// @group base
////

audio,
embed,
iframe,
object,
video {
    @extend %media-elements !optional;
}

/**
 * Fix `audio`, `iframe`, and `video`
 */
audio,
iframe,
video {
    width: 100%;
    border: 0;
    overflow-x: hidden;
}

iframe {
    contain: strict;
}

/**
 * Images and Figures
 */
img,
svg,
picture {
    @extend %image-elements !optional;
}

%image-elements {
    display: block;
    overflow: hidden;
    position: relative;

    &,
    a & {
        max-width: 100%;
        height: auto;
    }
}

img {
    border-width: 0;
}

svg:not(:root) {
    overflow: hidden;
}
