////
/// Vertical split
/// @group components
////

.vertical-split {
    background-image: linear-gradient(
        to top,
        $color-concrete 50%,
        $color-white 50%
    );
}

.vertical-split--reverse {
    background-image: linear-gradient(
        to bottom,
        $color-concrete 50%,
        $color-white 50%
    );
}

.vertical-split--hub {
    background-image: linear-gradient(
        to bottom,
        $color-hub 50%,
        $color-white 50%
    );
}

.vertical-split--catskill-white {
    background-image: linear-gradient(
        to top,
        $color-catskill-white 50%,
        rgba(0, 0, 0, 0) 50%
    );
}

.vertical-split--purple {
    background-image: linear-gradient(
        to top,
        $color-gallery 50%,
        $color-hubsecondary 50%
    );
}

.vertical-split--purple--75pct {
    background-image: linear-gradient(
        to top,
        $color-gallery 25%,
        $color-hubsecondary 0
    );
}

.vertical-split--nested-search {
    background-image: linear-gradient(
        to top,
        $color-gallery 33%,
        $color-white 0
    );
}

.vertical-split--lightgrey {
    background-image: linear-gradient(
        to top,
        $color-catskill-white 50%,
        $color-lightgrey 50%
    );
}

.vertical-split--indicator {
    background-image: linear-gradient(
        to top,
        $color-white 50%,
        $color-gallery 50%
    );
}

.vertical-split--indicator--reverse {
    background-image: linear-gradient(
        to top,
        $color-gallery 50%,
        $color-white 50%
    );
}

.vertical-split--embedded {
    margin-top: $measure-enormous;
}
