////
/// Variables
/// @group abstracts
////

/// @type Length
$base-font-size: 16px !default;

/// @type Length
$grid-width-min: 320px !default;
/// @type Length
$grid-width-small: 500px !default;
/// @type Length
$grid-width-between: 650px !default;
/// @type Length
$grid-width-medium: 900px !default;
/// @type Length
$grid-width-large: 1200px !default;
/// @type Length
$grid-width-extra-large: 1440px !default;
/// @type Map
/// @prop {String} key - Breakpoint name
/// @prop {Length} value - Breakpoint value
/// @require {Variable} $grid-width-small
/// @require {Variable} $grid-width-medium
/// @require {Variable} $grid-width-large
$breakpoints: (
    'min': $grid-width-min,
    'small': $grid-width-small,
    'between': $grid-width-between,
    'medium': $grid-width-medium,
    'large': $grid-width-large,
    'extralarge': $grid-width-extra-large,
) !default;

/// @type Number
$grid-columns: 12 !default;
/// @type Length
$grid-padding: 1rem !default;
/// @type Length
/// @require {Variable} $grid-padding
$grid-padding-half: ($grid-padding / 2) !default;
/// @type Length
$fixed-sidebar-width: 17.5rem !default;
// 21em
/// @type Length
$line-length-minimum: #{$base-font-size * 21} !default;
// 30em
/// @type Length
$line-length-maximum: #{$base-font-size * 30} !default;
/// @type Length
$line-length-responsive: calc(
    #{$line-length-minimum} + (
            #{strip-units($line-length-maximum)} - #{strip-units(
                    $line-length-minimum
                )}
        ) * ((100vw - #{$grid-width-small}) /
                (
                    #{strip-units($grid-width-large)} - #{strip-units(
                            $grid-width-small
                        )}
                ))
);

/// @type Length
$measure-xsmall: 0.25rem !default;
/// @type Length
$measure-small: 0.5rem !default;
/// @type Length
$measure-small-relative: 0.5em !default;
/// @type Length
$measure-small-proportional: 2vmin !default;
/// @type Length
$measure-medium: 1rem !default;
/// @type Length
$measure-medium-relative: 1em !default;
/// @type Length
$measure-medium-proportional: 4vmin !default;
/// @type Length
$measure-large: 2rem !default;
/// @type Length
$measure-large-relative: 2em !default;
/// @type Length
$measure-large-proportional: 8vmin !default;
/// @type Length
$measure-xlarge: 3rem !default;
/// @type Length
$measure-xxlarge: 3.25rem !default;
/// @type Length
$measure-enormous: 3.5rem !default;

/// @type Number
$opacity-tint: 0.1 !default;
/// @type Number
$opacity-fifth: 0.2 !default;
/// @type Number
$opacity-half: 0.5 !default;
/// @type Number
$opacity-shade: 0.85 !default;
/// @type Map
/// @prop {String} key - Opacity name
/// @prop {Number} value - Opacity value
/// @see {Function} opacities
$opacities: (
    'tint': $opacity-tint,
    'fifth': $opacity-fifth,
    'half': $opacity-half,
    'shade': $opacity-shade,
) !default;

