.home--news-and-events {
    .tabs {
        .tabs__menu-wrapper {
            @include grid-column(4);
            @include grid-column-push(4);
        }
    }
}

.feature-news {
    position: relative;
}

.feature-news__cards {
    @include grid;
}

.feature-news__card {
    @include grid-column-map(
        (
            'small': 12,
            'between': 4,
        )
    );
}

.feature-news__card {
    img {
        display: none;
        width: 100%;
    }

    .feature-news__details {
        padding: $measure-medium;
        text-align: center;
    }

    &:first-of-type {
        img {
            display: block;
        }
    }

    &:nth-of-type(2),
    &:nth-of-type(3) {
        img {
            @include media('>between') {
                display: block;
            }
        }

        .feature-news__details {
            @include media('<=between') {
                text-align: left;
            }
        }
    }
}

.feature-news__card__title {
    @include anchor(quick-link--feature);

    font-family: $font-family-lato;
    font-size: $font-size-delta;
    font-weight: $font-weight-heavy;
    white-space: normal;
}

.feature-news__card__published {
    margin-top: $measure-medium;

    @include media('<=between') {
        margin-top: $measure-small;
    }
}

.feature-news__card__category {
    @include anchor('purple');
    font-size: $font-size-small;
    font-weight: $font-weight-semibold;
}

.feature-news__link-more {
    font-family: $font-family-lato;
    font-weight: $font-weight-heavy;

    a {
        @include anchor(skate);

        span {
            font-weight: $font-weight-default;
        }
    }

    position: relative;
    margin: $measure-medium 0 0;
    float: right;
}

.item__date {
    font-size: $font-size-small;
}
