////
/// CTA Group
/// @group components
////

$cta-width-minimum: 10rem;

.cta-group {
    position: relative;

    @include media('>small') {
        min-width: $cta-width-minimum;
    }

    & + & {
        @include media('>small') {
            margin-left: $grid-padding;
        }

        @include media('<=small') {
            margin-top: $grid-padding;
        }
    }
}

.cta-group--centered {
    text-align: center;
}

.cta-group--stretched {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    font-size: 0;
    width: 100%;

    .cta {
        font-size: $font-size-default;

        @include media('<=between') {
            flex: 1 1 100%;
        }

        @include media('>between') {
            flex: 1 0 auto;
        }
    }

    .cta + .cta {
        @include media('>between') {
            margin-left: $measure-small;
        }
    }

    .fa {
        float: right;
        padding-top: #{$measure-small / 2};
    }
}

.cta-group__grid--halves {
    @include media('>small') {
        @supports (display: flex) {
            display: flex;
        }
    }

    .cta-group {
        @include media('>small') {
            float: left;

            @supports (display: flex) {
                float: none;
            }
        }
    }
}

.cta-group--bottom {
    align-self: flex-end;
}

.cta-group--vertical {
    @include media('>small') {
        text-align: center;

        @supports (display: flex) {
            display: flex;
            flex-direction: column;
            justify-content: space-between;
        }
    }

    .cta + .cta {
        margin-top: $grid-padding;
    }
}

.cta-group--overhang-bottom {
    @include media('>small') {
        height: 0;
        padding-bottom: 100%;
    }

    .cta {
        @include media('>small') {
            @include absolute(full);
        }
    }

    .cta-group--overhang-bottom__icon {
        @include media('<=small') {
            @include absolute(right, #{$measure-medium * 1.5});
        }

        @include media('>small') {
            @include absolute(bottom-right, #{$measure-medium * 1.5});
            bottom: #{$measure-medium * 1.5};
        }
    }
}

.cta-group--margin {
    margin-top: $measure-large;

    @include media('>small') {
        margin-top: $measure-small;
    }

    .cta-group--margin__icon {
        @include absolute(right, #{$measure-medium * 1.5});
    }

    @include media('>small') {
        width: #{($cta-width-minimum * 2) + $measure-medium};
    }

    @include media('>medium') {
        width: #{$cta-width-minimum * 1.75};
    }
}

.cta-group--horizontal {
    .cta {
        text-align: center;
        width: 100%;
        margin-top: $measure-medium;
        padding: $measure-small;
    }
}
